.purchases {
	padding: {
		top: 40px;
		bottom: 150px; }

	color: $darkblue;

	@include media(767px) {
		padding: {
			top: 30px;
			bottom: 30px; } }

	&__mob-btns {
		@extend .catalog__mob-btns;
		margin-bottom: 20px; }

	&__menu-trigger {
		@extend .catalog__menu-trigger; }

	&__tabs {
		display: flex;

		border-bottom: 1px solid #dfe7eb; }

	&__tab {
		position: relative;

		margin-right: 2.32em;

		font-family: "Mont";
		font-size: 1.5625em;
		font-weight: 300;
		line-height: 2.48;
		white-space: nowrap;

		&:last-child {
			margin-right: 0; }

		@include media(1199px) {
			font-size: 1.25em; }

		@include media(575px) {
			margin-right: 1em;
			font-size: 1em; }

		&_active {

			font-weight: 800;

			&::after {
				content: '';
				position: absolute;
				top: 100%;
				left: 0;

				width: 100%;
				height: 6px;
				background-color: $darkblue;

				transform: translateY(-50%);

				@include media(575px) {
					height: 4px; } } }

		a,button {
			display: inline-block;
			margin-right: 1.6em;

			border: none;
			background: none;
			padding: 0;
			appearance: none;
			text-decoration: none;
			color: inherit;
			font-family: inherit;
			font-weight: inherit;
			font-size: inherit;

			@include media(575px) {
				margin-right: 0.5em; } } }

	&__list {
		margin: {
			top: 50px; }
		padding: {
			left: 20px;
			right: 20px;
			bottom: 54px; }
		background-color: #ffffff; } }

.purchase {
	padding: {
		top: 40px;
		bottom: 40px;
		left: 50px;
		right: 50px; }
	border-bottom: 1px solid #dfe7eb;

	&:last-child {
		border-bottom: none; }

	@include media(1199px) {
		padding: 30px; }

	@include media(767px) {
		padding: {
			left: 0;
			right: 0; } }

	&__main {
		display: flex;
		justify-content: space-between;
		align-items: center;

		font-family: Poppins;
		font-size: 22px;
		font-weight: 400;

		@include media(1600px) {
			font-size: 18px; }

		@include media(1199px) {
			font-size: 16px; }

		@include media(575px) {
			position: relative;

			display: block;
			padding-right: 66px; } }

	&__order-number {
		flex-basis: 26.75%;
		text-transform: uppercase;
		white-space: nowrap;

		@include media(575px) {
			margin-bottom: 1em; } }
	&__price {
		flex-basis: 17.54%;
		text-transform: uppercase;
		white-space: nowrap;

		@include media(575px) {
			margin-bottom: 1em; } }
	&__date {
		flex-basis: 17.54%;

		@include media(575px) {
			margin-bottom: 1em; } }

	&__status {
		flex-basis: 17.54%; }


	&__btn {
		flex-basis: auto;

		@include media(575px) {
			position: absolute;
			bottom: 0;
			right: 0; } }

	&__show-details {
		width: 56px;
		height: 56px; }

	&__details {
		display: none;
		margin-top: 20px;

		&.visible {
			display: block; } }

	&__product {
		padding: 30px;
		border: 1px solid #dfe7eb;

		font-size: 20px;

		@include media(1199px) {
			padding: 15px;
			font-size: 16px; }

		@include media(767px) {
			padding: 10px;
			font-size: 14px; }

		.cart-product__info {

			@include media(575px) {
				display: block; } }

		.cart-product__price,
		.cart-product__total-price {
			@include media(575px) {
				margin: 0.3em 0;
				padding: 0; } } } }

