.choose-your-box {
	@extend %default-block;

	text-align: center;

	&_bb {
		padding-left: 0;

		@include media(991px) {
			width: 100vw;
			margin-left: -30px;
			padding: {
				left: 15px;
				right: 15px; }
			background-color: #f6fafc; }

		@include media(767px) {
			margin-left: -15px; } }

	&__items {
		margin-top: 65px;
		overflow: visible;

		.swiper-slide {
			height: auto;

			@include media(767px) {
				width: 255px; } }

		.swiper-pagination {
			position: static;

			margin-top: 52px; }

		.product-tile__buttons {
			@include media(1199px) {
				button {
					font-size: 10px; } } } }

	&__product {

		@include media(991px) {
			@media (min-width: 768px) {
				.product-tile__hidden {
					font-size: 8px; } } } }

	&__bb-trigger {
		display: flex;
		flex-direction: column;
		align-items: flex-start;
		justify-content: flex-end;
		height: 100%;
		padding: (60em / 18) (40em / 17);
		box-sizing: border-box;

		box-shadow: 0 2px 11px 8px rgba(79, 156, 194, 0.05);
		background-color: #0e3d5e;
		color: #fff;
		text-align: left;

		h3 {
			margin-top: (15em / 24);
			font-family: "Mont";
			font-size: (24em / 18);
			font-weight: 800;
			line-height: (36 /24); }

		button {
			margin-top: (50em / 18); }

		svg {
			width: 60px;
			height: 44px; }

		@include media(1199px) {
			padding: 30px 20px; }

		@include media(767px) {
			flex-basis: 100%; } }

	&__show-more {
		margin: {
			top: (110em / 18); }

		text-align: center;

		@include media(767px) {
			margin: {
				top: 40px; } } } }
