.nav {
	font-size: 16px;
	font-weight: 500;

	&__list {
		display: flex;
		justify-content: space-between;

		@include media(767px) {
			display: block; } }

	&__item {
		cursor: pointer;
		white-space: nowrap;

		&_has-submenu {
			position: relative;

			svg {
				position: absolute;
				left: 100%;
				top: 50%;
				width: 9px;
				height: 6px;
				padding: {
					left: 9px;
					top: 5px;
					bottom: 5px; }
				transform: translateY(-50%); }

			.submenu {
				visibility: hidden;
				opacity: 0;
				transform: translateX(-33%) translateY(5%);
				transition-property: visibility, opacity, transform;
				transition-duration: 0.3s;
				transition-delay: 0.3s; }

			&:hover {
				.submenu {
					visibility: visible;
					opacity: 1;
					transform: translateX(-33%) translateY(0);
					transition-delay: 0s; } } }

		a, span {
			position: relative;
			cursor: pointer;
			user-select: none;

			&::after {
				content: '';

				position: absolute;
				top: 100%;
				left: 0;

				width: 100%;
				height: 1px;
				background-color: currentColor;

				transform-origin: 100% 0;
				transform: scaleX(0);
				transition: transform 0.3s; }

			&:hover::after {
				transform-origin: 0 100%;
				transform: scaleX(1); } }

		@include media(1199px) {
			margin-right: 3.9vw; }


		@include media(767px) {
			margin-right: 0;
			margin-bottom: 27px; } }


	@include media(767px) {
		display: flex;
		flex-direction: column;
		justify-content: flex-start;
		align-items: center;

		font-size: 20px;
		line-height: 1.5;
		text-align: center;


		width: 100%;
		height: calc(100vh - 95px);
		padding: {
			bottom: 57px;
			top: 40px; }

		color: #fff; } }

.submenu {
	position: absolute;
	top: 100%;
	left: 0;
	z-index: 10;

	width: 800px;
	margin-top: 2em;

	font-size: 16px;

	transform: translateX(-33%);
	filter: drop-shadow(0 0 18px rgba(6, 27, 41, 0.1));

	cursor: default;

	@include media(1599px) {
		width: 40vw;

		font-size: 14px; }

	@include media(1399px) {
		font-size: calc(0.5vw + 6px); }

	&__wrapper {
		position: relative;

		display: flex;
		justify-content: space-between;
		width: 100%;
		padding: 70px;
		background-color: #fff;
		color: $darkblue;

		box-sizing: border-box;

		&::before {
			content: '';

			position: absolute;
			left: 35%;
			top: 0;

			width: 20px;
			height: 20px;
			background-color: #fff;

			transform: translateY(-50%) rotate(-45deg); }

		@include media(1599px) {
			padding: 2vw; } }

	&__title {
		margin-bottom: 2.6em;
		color: $blue;

		@include media(1599px) {
			margin-bottom: 2em; } }

	&__list {
		li {
			margin-bottom: 2.6em;
			&:last-child {
				margin-bottom: 0; }

			@include media(1599px) {
				margin-bottom: 2em; } } } }

