.table {
	display: flex;
	background-color: #ffffff;
	font-size: 1em;
	overflow: auto;

	& > div {
		flex-shrink: 0;
		flex-grow: 1;
		padding: {
			left: 20px;
			right: 20px; }

		@include media(1199px) {
			font-size: 0.875em; } }

	table {
		width: 100%; }

	thead th {
		padding: 1.25em;

		font-weight: 600;
		color: $blue;
		white-space: nowrap;

		&:first-child {
			padding: {
				left: 3.125em; }
			text-align: left;

			@include media(575px) {
				padding: {
					left: 0; } } }
		&:last-child {
			padding: {
				right: 3.125em; }
			text-align: right;

			@include media(575px) {
				padding: {
					right: 0; } } } }

	tr {
		border-bottom: 1px solid #dfe7eb; }

	tbody {
		tr:last-child {
			border-bottom: none; }

		th, td {
			padding: {
				top: 1.5em;
				bottom: 1.5em;
				left: 1em;
				right: 1em; }

			font-size: 1.25em;
			line-height: 1.7;
			white-space: nowrap;

			@include media(1600px) {
				font-size: 1.142em; } }

		th {
			font-weight: 600;
			padding: {
				left: 2.5em; }
			text-align: left;

			@include media(575px) {
				padding: {
					left: 0; } } }

		td {
			text-align: center;
			&:first-child {
				padding: {
					left: 2.5em; }
				text-align: left; }
			&:last-child {
				padding: {
					right: 2.5em; }
				text-align: right; }

			@include media(575px) {
				&:first-child {
					padding-left: 0; }
				&:last-child {
					padding: {
						right: 0; } } } } } }
