.about-us {
	padding: {
		top: (150em / 18);
		bottom: (150em / 18); }

	font-size: 18px;

	&_bbg {
		padding: {
			top: (130em / 18); } }

	&_businesses {
		padding: {
			top: (120em / 18); } }

	@media screen and (min-width: 768px) and (max-width: 1600px) {
		font-size: 16px; }

	@media screen and (min-width: 1200px) and (max-width: 1459px) {
		padding-left: 8vw; }

	@include media(767px) {
		padding: {
			top: 40px;
			bottom: 40px; } }

	&__row {
		display: flex;

		@include media(767px) {
			flex-wrap: wrap; } }

	&__col {
		flex: 1 1 auto;

		box-sizing: border-box;

		@include media(767px) {
			flex-basis: 100%;
			padding-top: 2em;

			&:first-child {
				padding-top: 0; } }

		&_basis-320 {
			@media screen and (min-width: 768px) {
				flex: 0 0 (320em /18); } }

		&_basis-360 {
			@media screen and (min-width: 768px) {
				flex: 0 0 (360em /18); } }

		&_basis-50-p {
			@media screen and (min-width: 768px) {
				flex: 0 0 50%; } }

		&_pl-100 {
			@media screen and (min-width: 768px) {
				padding: {
					left: (99em / 18); } } }

		&_pl-60 {
			@media screen and (min-width: 768px) {
				padding: {
					left: (60em / 18); } } }

		&_pl-38 {
			@media screen and (min-width: 768px) {
				padding: {
					left: (38em / 18); } } }

		&_pr-38 {
			@media screen and (min-width: 768px) {
				padding: {
					right: (38em / 18); } } } }

	&__title {

		&_mb-70 {
			margin: {
				bottom: (70em / 50); } } }

	&__image {
		img {
			max-width: 100%; }

		@include media(767px) {
			text-align: center; } }

	&__box {
		display: flex;
		flex-direction: column;
		justify-content: flex-end;
		align-items: flex-start;
		min-height: (495em / 14);
		padding: 30px;

		font-size: 14px;
		background-color: $darkblue;
		color: #fff;
		box-sizing: border-box;

		@include media(767px) {
			min-height: 0; }

		p {
			margin-top: (38em / 16);

			font-style: italic;
			line-height: (30 / 14); }

		.button {
			margin: {
				top: 3.3em; } } }
	&__icon {
		max-height: (60em / 14); }

	&__text-content {
		line-height: (30 / 18);

		p {
			margin-bottom: (30em / 18);

			&:last-child {
				margin-bottom: 0; } }

		strong {
			font-size: (24em / 18); }

		@include media(767px) {
			text-align: center; }

		&_limited-text {
			@media screen and (min-width: 1200px) {
				padding-right: 10%; } } }


	&__bbg-header {
		display: flex;
		flex-direction: column;
		margin: {
			bottom: (66em / 18); }

		font-family: "Mont";

		h3 {
			order: -1;
			font-size: (20em / 18);
			font-weight: 800;
			letter-spacing: 3.75px;
			text-transform: uppercase; }

		@include media(767px) {
			margin-bottom: 20px;
			h3 {
				text-align: center; } } } }
