.sidebar-bluebox {

	@include media(991px) {
		display: flex;
		justify-content: center;
		flex-wrap: wrap; }

	&__top {
		display: flex;

		@include media(991px) {
			flex-wrap: wrap;
			flex-basis: 343px; }

		@include media(767px) {
			margin-left: 0;
			margin-right: auto; }

		@include media(575px) {
			flex-basis: 100%; } }

	&__level {
		display: flex;
		align-items: flex-end;
		justify-content: center;
		width: 39px;
		height: 223px;
		margin-right: 1px;
		padding: {
			bottom: 7px; }

		font-size: 15px;
		font-weight: 600;
		letter-spacing: -0.63px;

		background: linear-gradient(180deg, #f3fcff 0%, #64b2db 100%);
		color: #ffffff;

		box-sizing: border-box; }

	&__image {
		flex-basis: calc(100% - 40px);
		max-width: calc(100% - 40px);
		height: 223px;
		box-sizing: border-box;

		img {
			display: inline-block;
			display: inline-block;
			width: 100%;
			height: 100%;
			object-fit: cover; } }

	&__mob-add {
		display: none;

		@include media(991px) {
			flex-basis: 100%;
			display: flex;
			justify-content: center;
			align-items: center;
			min-height: 110px;
			margin-top: 4px;

			background-color: #f4f9fb; } }

	&__drop-zone {
		margin: 10px 0;
		padding: 14px 29px;
		border: 1px dashed $blue;

		font-size: 14px;
		line-height: (17 / 14);

		background-color: #ffffff;
		border-radius: 2px;
		color: rgba($blue, 0.5);

		@include media(991px) {
			display: none; } }

	&__mob-header {
		display: none;

		margin-bottom: 12px;

		font-family: "Mont";
		font-size: 20px;
		font-weight: 800;

		text-align: left;
		color: $blue;

		@include media(991px) {
			display: block; } }

	&__items {

		box-sizing: border-box;

		@include media(991px) {
			flex-basis: calc(100% - 343px);
			padding-left: 30px; }

		@include media(767px) {
			flex-basis: 100%;
			padding-left: 0;
			padding-top: 30px; } }


	&__total {
		display: flex;
		justify-content: center;
		margin-top: 114px;
		margin-bottom: 17px;

		font-size: 24px;
		letter-spacing: -0.25px;

		h3 {
			margin-right: 0.1em; }

		span {
			font-weight: 600;

			color: $blue; }

		@include media(991px) {
			flex-basis: 100%; }

		@include media(767px) {
			margin-top: 35px; } } }

.sm-product {
	position: relative;

	display: flex;
	align-items: center;
	margin-bottom: 6px;
	padding: 10px;
	border: 1px solid #e2e7ea;

	border-radius: 2px;
	background-color: #ffffff;

	text-align: left;

	&:hover {
		box-shadow: 0 0 5px rgba(40, 86, 111, 0.14); }

	&:hover & {

		&__delete {
			opacity: 1; } }

	&__image {
		min-width: 55px; }

	&__delete {
		position: absolute;
		top: 10px;
		right: 10px;

		width: 13px;
		height: 13px;
		padding: 0;
		border: none;

		line-height: 0;

		border-radius: 50%;
		appearance: none;
		background: none;
		opacity: 0;
		cursor: pointer;

		svg {
			width: 100%;
			height: 100%; }

		&:focus {
			outline: none;
			box-shadow: 0 0 0 1px #fff, 0 0 2px 2px $blue; } }

	&__title {
		padding: 0 12px;

		font-weight: 600;
		font-size: 12px;
		letter-spacing: -0.15px;
		line-height: (14 / 12); }

	&__price {
		margin-right: 0;
		margin-left: auto;

		font-size: 15px;
		font-weight: 600;

		white-space: nowrap;
		color: $blue; } }
