.split {

	&_lightgray {
		background-color: #f6fafc; }

	&_darkblue {
		background-color: $darkblue;
		color: #fff; }

	&__row {
		display: flex;
		flex-wrap: wrap; }

	&__col {
		display: flex;
		flex-basis: 50%;
		min-height: 100vh;

		box-sizing: border-box;

		&:not(.split__col_picture):nth-child(odd) {
			justify-content: flex-end;
			padding-left: 138px;

			@include media(1199px) {
				padding-left: 0;

				.box {
					padding-left: 30px; } }

			@include media(767px) {
				.box {
					padding-left: 16px; } } }

		&:nth-child(even) {
			justify-content: flex-start; }

		@include media(767px) {
			flex-basis: 100%; }

		&_picture {
			padding-left: 0;

			&>* {
				width: 100%; }

			img {
				width: 100%;
				height: 100%;
				object-fit: cover; }

			@include media(767px) {
				display: none; } } } }


.box {
	width: 100%;
	max-width: 822px;
	height: 100%;
	padding: {
		top: 5em;
		left: 5.75em;
		right: 5em;
		bottom: 5em; }

	font-size: 20px;

	box-sizing: border-box;

	@include media(1399px) {
		padding: {
			top: 3em;
			left: 3.1em;
			right: 3em;
			bottom: 3em; }
		font-size: 16px; }

	@include media(1199px) {
		padding: {
			top: 56px;
			left: 50px;
			right: 50px;
			bottom: 55px; } }

	@include media(767px) {
		padding: {
			top: 36px;
			left: 16px;
			right: 16px;
			bottom: 50px; }

		text-align: center; }

	&_no-pr-lg {
		@media screen and (min-width: 1200px) {
			padding-right: 0; } }

	&__title {
		span {
			display: block; } }

	&__img {
		display: none;
		max-width: 100%;
		margin-top: 43px;

		img {
			max-width: 100%; }

		@include media(767px) {
			display: block; } }


	&__desc {
		max-width: 28.25em;
		margin-top: 3.3em;

		color: $darkblue;
		font-size: 1em;
		font-weight: 300;
		line-height: 1.6;

		&_bluebox {
			margin-top: 102px;
			margin-bottom: 210px; }

		@include media(1399px) {

			&_bluebox {
				margin-top: 5.1em;
				margin-bottom: 10.5em; } }

		@include media(1199px) {
			max-width: 100%;
			margin-top: 2.25em;

			&_bluebox {
				margin-top: 30px;
				margin-bottom: 38vh; } }

		@include media(767px) {
			text-align: center;

			&_bluebox {
				margin: {
					top: 28px;
					bottom: 35px; } } } }

	&__advantages {
		display: flex;
		justify-content: space-between;
		max-width: 34.3em;
		margin-top: 6em;
		margin-bottom: 4em;
		padding-right: 1em;

		color: $darkblue;
		font-size: 1em;
		font-weight: 300;
		line-height: 1.3;

		box-sizing: border-box;

		svg {
			width: 52px;
			height: 52px;

			&.coffee-cup {
				width: 54px;
				height: 59px; } }

		div:first-child {
			display: flex;
			align-items: flex-end;
			height: 54px;

			color: $blue; }

		div:last-child {
			margin-top: 26px; }

		@include media(1199px) {
			flex-wrap: wrap;
			justify-content: flex-start;
			max-width: 350px;
			margin-top: 1.5em;
			padding-right: 0;

			font-size: 15px;
			line-height: 1.73;
			text-align: center;

			li {
				flex-basis: 40%;
				margin: {
					bottom: 20px;
					right: 4.89vw; }

				&:nth-child(even) {
					margin-right: 0; } }

			div {
				margin: {
					left: auto;
					right: auto; }
				&:first-child {
					display: block; }
				&:last-child {
					max-width: 9.4em; } }

			br {
				display: none; } }

		@include media(767px) {
			justify-content: center;
			max-width: 100%;
			margin-bottom: 15px;

			li {
				flex-basis: 34%;
				margin: {
					bottom: 40px;
					right: 0; } } } }


	&__subtitle {
		color: $blue;
		font-family: Poppins;
		font-size: 1.1em;
		font-weight: 700;

		@include media(1199px) {
			font-size: 1.25em; }
		@include media(767px) {
			text-align: center; } }

	&__button {
		margin-top: 1.8em;

		@include media(767px) {
			margin-top: 1.875em; } } }


