.modal {
	position: fixed;
	left: 50%;
	top: 50%;

	padding: 20px 34px;

	font-size: 18px;

	box-sizing: border-box;

	box-shadow: 0 0 18px rgba(6, 27, 41, 0.1);
	background-color: #ffffff;
	color: $darkblue;
	text-align: center;

	transform: translate(-50%, -30%) perspective(400px) rotateX(-5deg);
	transition: transform 0.3s, opacity 0.3s;
	opacity: 0;
	pointer-events: none;

	&.visible {
		transform: translate(-50%, -50%) perspective(400px) rotateX(0);
		opacity: 1;
		pointer-events: auto; }

	&__close {
		position: absolute;
		top: 20px;
		right: 20px;

		width: 20px;
		height: 20px;
		padding: 0;
		border: none;

		line-height: 0;

		background: none;
		appearance: none;
		border-radius: 50%;
		color: rgba($blue, 0.3);
		cursor: pointer;

		transition: color 0.2s;


		svg {
			width: 100%;
			height: 100%; }

		&:focus {
			outline: none;
			box-shadow: 0 0 0 2px #fff, 0 0 2px 4px $blue; }

		&:hover {
			color: $blue; } }

	&__title {
		font-weight: 600;
		line-height: 30px;
		color: $blue; }

	&__text {
		margin-bottom: 23px;
		font-size: 16px;
		font-weight: 300;
		line-height: 20px;

		a {
			text-decoration: none;
			color: $blue; } }

	&__icon {
		margin-top: 30px; }

	&__button {
		min-width: 90px;
		padding-top: 10px;
		padding-bottom: 10px;
		font-size: 14px; }

	&__reject {
		position: relative;

		font-size: 14px;
		line-height: 20px;
		padding: 0;
		border: none;

		background: none;
		appearance: none;
		color: $blue;

		&::after {
			content: '';
			position: absolute;
			bottom: 0;
			left: 0;

			width: 100%;
			height: 1px;

			background-color: $blue; } }


	&_cookies {
		max-width: 365px;

		text-align: left; }

	&_cookies & {

		&__title {
			padding-right: 30px; } } }
