.white-card {
	@extend .offer-tile__wrapper;
	text-align: left;
	background-color: #fff;

	&__title {
		margin-top: (115em / 40);
		font-family: "Mont";
		font-size: (40em / 18);
		font-weight: 800;
		line-height: (62 / 40);

		@include media(1199px) {
			margin-top: 0.6em;
			font-size: (30em / 18); } }


	&__text {
		margin-top: (30em / 25);
		font-size: (25em / 18);
		font-weight: 300;
		line-height: (37 / 25);

		@include media(1199px) {
			margin-top: 13px;
			font-size: 1em;
			line-height: (22 / 18); } }

	&__icon {
		max-height: (106em / 18);

		svg {
			max-width: 100%; }

		&_open-book {
			svg {
				height: 93px; } }

		&_idea {
			svg {
				height: 106px; } } }

	&__button {
		margin-top: (40em / 18);
		margin-bottom: 10px;

		@include media(1199px) {
			margin-top: 1.5em; } } }
