.step {
	width: 100%;
	height: 100%;

	padding: {
		left: 30px;
		top: 28px;
		right: 28px;
		bottom: 24px; }

	box-shadow: 0 2px 11px 8px rgba(79, 156, 194, 0.05);
	background-color: #ffffff;

	box-sizing: border-box;
	transition: box-shadow 0.3s;

	&:hover {
		box-shadow: 0 2px 11px 8px rgba(79, 156, 194, 0.2); }

	&__head {
		display: block;

		color: $darkblue;
		font-size: 14px;
		font-weight: 500;
		text-align: right;

		span {
			font-size: 20px;
			padding-left: 6px; } }

	&__icon {
		display: block;
		width: 90px;
		height: 90px;
		margin-top: 158px;

		color: $blue;

		@include media(767px) {
			margin-top: 101px; } }

	&__text {
		display: block;
		margin-top: 55px;

		color: #2a2727;
		font-size: 20px;
		font-weight: 300;
		line-height: 25px; } }
