.input-text {
	position: relative;
	line-height: 1.5;
	&__label {
		display: block;
		margin: {
			bottom: 5px; }
		opacity: 0.3;
		font-size: 0.875em; }

	&__input {
		width: 100%;

		border: none;
		border-bottom: 1px solid #dfe7eb;

		background: none;
		appearance: none;
		color: inherit;
		font-family: inherit;
		font-size: 1.125em;
		box-sizing: border-box;

		&_has-icon {
			padding-right: 30px; }

		&:focus {
			outline: none;
			border-bottom-color: darken(#dfe7eb, 20%); }

		&::placeholder {
			font-size: 12px; } }

	&__icon {
		position: absolute;
		right: 0;
		bottom: 0.25em;

		width: 13px;
		height: 13px; }

	&__show-hide {
		position: absolute;
		right: 0;
		bottom: 0.25em;
		color: rgba($blue, 0.3);

		background: none;
		border: none;
		appearance: none;

		svg {
			width: 17px;
			height: 11px; }

		&:focus {
			outline: none; }

		&.active {

			color: $blue; } } }
