.sidebar-order {

	&__image {
		img {
			max-width: 100%; } }

	&__list {
		padding: {
			top: (30em / 18); }

		border-bottom: 1px solid #d8e3ea; }

	&__list-item {
		display: flex;
		justify-content: space-between;
		margin: {
			bottom: (23em / 18); }

		font-weight: 300; }

	&__key {
		padding-right: 0.5em;
		color: #b1bdc2; }

	&__val {
		padding-left: 0.5em; }

	&__coupon {
		margin: {
			top: (20em / 14);
			bottom: (20em / 14); }
		padding: 0;
		border: none;

		font-size: (14em / 18);
		font-weight: 300;

		background: none;
		color: #d8e3ea;
		appearance: none;
		text-decoration: underline; }

	&__change {
		margin: {
			top: (50em / 18); } } }
