.cart-product {
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding: {
		top: 30px;
		bottom: 30px; }

	@include media(575px) {
		font-size: 0.75em; }


	&__image {
		flex-basis: 10%;
		max-width: 10%;
		display: flex;
		max-height: 100px;
		min-height: 1px;

		img {
			max-height: 100%; }

		@include media(575px) {
			flex-basis: auto;
			max-width: 30%; } }

	&__info {
		flex: 1 1 auto;
		display: flex;
		justify-content: space-between;
		align-items: center;
		padding-left: 50px;

		@include media(1199px) {
			padding-left: 5%; }

		@include media(575px) {
			padding-left: 2%; }

		@include media(399px) {
			flex-direction: column;
			align-items: flex-start; } }

	&__title {
		font-size: 21px;
		font-weight: 700;
		letter-spacing: 0.12em;
		text-transform: uppercase;

		@include media(575px) {
			font-size: 1.2em; }

		@include media(399px) {
			margin-bottom: 0.2em; } }

	&__desc {
		max-width: 14.3em;
		font-size: 0.875em;
		font-weight: 300;
		line-height: 1.57; }

	&__price {
		padding: 0 5px;
		white-space: nowrap;
		text-transform: uppercase;

		@include media(399px) {
			margin-bottom: 1em;
			padding: 0; } }

	&__quantity {
		padding: 0 5px;
		text-transform: none;
		white-space: nowrap;

		@include media(575px) {
			display: none; }

		@include media(399px) {
			padding: 0; } }

	&__mob-quantity {
		display: none;

		@include media(575px) {
			display: inline; } }

	&__total-price {
		padding: 0 5px;
		font-weight: 600;
		white-space: nowrap;
		text-transform: uppercase;

		@include media(399px) {
			padding: 0; } } }
