.our-companies {
	padding: {
		top: (116em / 18);
		bottom: (160em  / 18); }

	font-size: 18px;

	background-color: #f6fafc;

	@media screen and (min-width: 768px) and (max-width: 1600px) {
		font-size: 16px; }

	@media screen and (min-width: 1200px) and (max-width: 1459px) {
		padding-left: 8vw; }

	@include media(767px) {
		padding: {
			top: 40px;
			bottom: 40px; } }

	&__title {
		text-align: center; }

	&__list {
		margin-top: (72em / 18);
		padding: (15em / 18);

		@include media(1199px) {
			max-width: (1140em / 18);
			margin: auto; } }

	&__item {
		width: auto; }

	&__pagination {
		display: flex;
		justify-content: center;
		padding-top: 1em;

		&.swiper-pagination-lock {
			display: none; } } }
