.burger {
	position: relative;

	width: 48px;
	height: 42px;
	padding: 10px;
	border: none;

	background: none;
	appearance: none;

	transition: box-shadow 0.1s ease-in-out;

	&__line {
		position: absolute;
		left: 10px;

		display: block;
		width: 28px;
		height: 4px;

		background-color: $darkblue;
		border-radius: 2px;

		&:nth-child(1) {
			top: 10px;
			transition: transform 0.3s ease-in-out; }

		&:nth-child(2) {
			top: 50%;
			transform: translateY(-50%);
			transition: opacity 0.3s ease-in-out; }

		&:nth-child(3) {
			bottom: 10px;
			transition: transform 0.3s ease-in-out; } }

	&.active & {

		&__line {

			&:nth-child(1) {
				transform: translate3d(0, 9px, 0) rotate(-45deg); }

			&:nth-child(2) {
				opacity: 0; }

			&:nth-child(3) {
				transform: translate3d(0, -9px, 0) rotate(45deg); } } }

	&:focus {
		outline: none;
		box-shadow: 0 0 2px 1px rgba(white, 0.3); } }


