.about-cafe {

	padding: {
		top: 164px;
		bottom: 165px; }

	background-color: #fff;

	font-size: 18px;

	box-sizing: border-box;

	@include media(1699px) {
		padding: {
			top: 100px;
			bottom: 100px;
			left: calc(8vw + 30px); } }

	@include media(1199px) {
		padding: {
			left: 30px;
			right: 30px;
			top: 70px;
			bottom: 70px; } }

	@include media(767px) {
		padding: {
			top: 16px;
			left: 16px;
			right: 16px;
			bottom: 50px; } }

	&__row {
		display: flex;
		max-width: 1385px;
		margin: auto;

		@include media(767px) {
			flex-wrap: wrap; } }

	&__col {
		box-sizing: border-box;

		&:first-child {
			flex: 0 0 460px;
			width: 460px;

			@include media(1399px) {
				flex-basis: 360px;
				width: 360px; }

			@include media(1199px) {
				flex-basis: 423px;
				width: auto; }

			@include media(949px) {
				flex-basis: 40%; }

			@include media(767px) {
				flex-basis: 100%; } }

		&:nth-child(2) {
			flex: 1 1 auto;
			padding-left: 10.9%;

			@include media(1399px) {
				padding-left: 5vw;
				padding-right: 30px; }

			@include media(1199px) {
				padding-left: 60px; }

			@include media(767px) {
				padding: {
					top: 40px;
					left: 16px;
					right: 16px; }
				text-align: center; } }

		&_steps {
			display: flex;
			flex-direction: column;
			justify-content: space-between;

			@include media(767px) {
				padding-left: 0!important;
				padding-right: 0!important; } } }

	&__header {
		padding: {
			top: 50px;
			bottom: 36px;
			right: 52px;
			left: 47px; }

		text-align: right;

		background-color: $darkblue;
		color: #fff;

		svg {
			width: 65px;
			height: 65px; }

		@include media(1399px) {
			padding: {
				top: 30px;
				bottom: 30px;
				right: 40px;
				left: 40px; } }

		@include media(1199px) {
			padding: {
				top: 60px;
				right: 52px;
				left: 40px;
				bottom: 40px; } }

		@include media(949px) {
			padding: {
				left: 30px;
				right: 30px; } }

		@include media(767px) {
			padding: 16px 16px 30px;
			text-align: center;

			svg {
				display: none; } } }

	&__title {
		margin-top: 3.66em;

		color: $blue;
		font-family: "Mont";
		font-size: 50px;
		font-weight: 800;
		line-height: 62px;

		span {
			display: block;
			margin-top: 11px;
			color: #ffffff;
			font-size: 0.76em;
			font-weight: 300;
			line-height: 1; }

		&_steps {
			margin-top: (120em / 50); }

		@include media(1399px) {
			font-size: 32px; }

		@include media(1199px) {
			margin-top: 113px;
			font-size: 30px;

			span {
				font-size: 1em; } }

		@include media(767px) {
			margin-top: 0;

			span {
				margin-top: 0; } } }


	&__desc {
		margin-top: 3.545em;
		font-size: 22px;
		font-weight: 400;
		line-height: 46px;

		@include media(1399px) {
			font-size: 18px; }

		@include media(1199px) {
			margin-top: 80px;
			font-size: 22px; }

		@include media(949px) {
			font-size: 16px;
			line-height: 27px; }

		@include media(767px) {
			max-width: 280px;
			margin: 35px auto 0;
			text-align: center; }

		&_steps {
			margin-top: 60px;

			@include media(1199px) {
				margin-top: 60px; } } }

	&__text {
		max-width: 640px;
		margin-top: -12px;

		color: #2a2727;
		font-size: 20px;
		font-weight: 300;
		line-height: 36px;

		p {
			margin-bottom: 1.8em; }

		@include media(767px) {
			max-width: 100%;
			margin-top: 0;
 }			// padding-top: 40px

		@include media(575px) {
			font-size: 16px;
			line-height: 2;

			p:last-child {
				display: none; } } }


	&__button {
		margin-top: 80px;

		@include media(1199px) {
			margin-top: 0;

			&_steps {
				margin-top: 30px; } }

		@include media(767px) {
			margin-top: 22px; } }

	&__step-icon {
		max-width: 65px;
		max-height: 65px;

		&_pack {
			width: 49px;
			height: 57px; }
		&_order {
			width: 58px;
			height: 65px; }
		&_storage {
			width: 61px;
			height: 65px; } }

	&__step {
		display: flex;
		align-items: baseline;
		max-width: (500em / 22);
		padding: 25px 0;

		font-size: (22em / 18);
		font-weight: 300;
		letter-spacing: 1px;
		line-height: (34 / 22);

		@include media(991px) {
			font-size: 1em; }

		@include media(767px) {
			margin: auto;
			text-align: left; }

		div p:first-child {
			margin: {
				top: (28em / 22);
				bottom: 10px; } } }

	&__step-header {
		width: 4.5em;
		padding-right: (25em / 30);

		font-size: (30em / 22);
		font-weight: 700;

		white-space: nowrap;

		@include media(767px) {
			font-size: 1em; } } }
