.benefits {
	@extend .our-companies;

	&__title {
		@extend .our-companies__title;
		color: $blue; }

	&__list {
		display: flex;
		justify-content: space-between;
		margin: {
			top: (70em / 18); }

		@include media(767px) {
			flex-wrap: wrap;
			margin-top: 40px; } }

	&__item {
		flex-basis: calc(33.333% - 20px);
		padding: {
			left: (30em / 18);
			right: (30em / 18);
			bottom: (60em / 18);
			top: (60em / 18); }

		box-sizing: border-box;

		box-shadow: 0 2px 11px 8px rgba(79, 156, 194, 0.05);
		background-color: #ffffff;
		color: #2a2727;

		@include media(1199px) {
			padding: 30px 20px;
			flex-basis: calc(33.333% - 10px); }

		@include media(767px) {
			flex-basis: 100%;
			margin-bottom: 20px;
			padding: 20px 10px; } }

	&__icon {
		display: flex;
		align-items: flex-end;
		height: (105em / 18);

		@include media(767px) {
			height: 80px; } }

	&__header {
		margin-top: (45em / 20);

		font-size: (20em / 18);
		font-weight: 600;
		line-height: (25 / 20); }

	&__text {
		margin-top: (25em / 16);

		font-size: (16em / 18);
		font-weight: 300;
		line-height: 1.45; } }
