.bank-card {
	width: 28.47em;
	max-width: 100%;
	height: 100%;
	min-height: 16.06em;
	font-size: 16px;
	border-radius: 0.625em;
	overflow: hidden;
	box-sizing: border-box;

	&_frontside {
		padding: {
			top: 1.875em;
			left: 1.5625em;
			right: 1.5625em;
			bottom: 1.875em; }
		background: url(../img/common/master-card.png) right 25px bottom 20px no-repeat, url(../img/common/bank-card-front-side.svg) center / 100% 100%; }

	&_backside {
		padding: {
			top: 1.875em; }

		background: #f3f2f2 url(../img/common/master-card.png) right 25px bottom 20px no-repeat; }

	@include media(575px) {
		width: 100%;
		min-height: 20em;
		font-size: 10px; }

	&__input {
		margin-bottom: 20px;
		color: #fff;

		&:last-child {
			margin-bottom: 0; }

		&_short {
			max-width: 4.375em;

			.input-text__input {
				padding-right: 0; } }

		.input-text__label {
			color: rgba(white, 0.8);
			text-transform: uppercase;
			font-size: 1em; } }

	&__strip {
		height: 3.625em;
		background-color: #2d2a2a; }

	&__backinfo {
		padding: {
			top: (39em / 16);
			left: (24em / 16);
			right: (24em / 16); } }

	&__row {
		display: flex;
		width: 100%;
		background-color: #dedcdc; }

	&__sign {
		flex: 1 0 auto;
		display: flex;
		justify-content: space-between;
		align-items: center;

		padding: {
			left: (23em / 16);
			right: (23em / 16); }

		opacity: 0.8;
		color: #171717;
		font-size: (25em / 16);
		font-weight: 500;
		letter-spacing: 0.69px;

		img {
			width: (97em / 25); } }

	&__cvv-wrapper {
		position: relative;
		flex-basis: (129em / 16);
		width: (129em / 16);
		height: 100%;
		border: 2px solid #dedcdc;
		background-color: #ffffff;

		input {
			width: 100%;
			height: 100%;
			padding: (7em / 16) (10em / 16);
			border: none;
			background: none;
			font-size: (27em / 16);
			box-sizing: border-box;
			line-height: 1; } }

	&__label {
		position: absolute;
		bottom: 100%;
		left: 2px;

		margin-bottom: 5px;

		color: #cbcbcb;
		font-size: 1em;
		letter-spacing: 0.8px;
		text-transform: uppercase; } }



.webp {
	.bank-card_frontside {
		background-image: url(../img/common/master-card.webp), url(../img/common/bank-card-front-side.svg); }
	.bank-card_backside {
		background-image: url(../img/common/master-card.webp); } }
