.testimonial-tile {
	position: relative;

	padding: {
		top: 189px;
		left: 40px;
		right: 40px;
		bottom: 190px; }

	background-color: #f6fafc;

	&::before {
		content: '';

		position: absolute;
		left: -14px;
		top: 30px;
		z-index: 1;
		width: 82px;
		height: 82px;
		background-image: url(../img/common/qoutes.png);
		background-size: contain;
		background-repeat: no-repeat;
		background-position: center; }

	@include media(1199px) {
		padding: {
			top: 128px;
			left: 20px;
			right: 5px; }

		&::before {
			left: 18px;
			top: 15px;

			width: 55px;
			height: 55px; } }

	@include media(767px) {
		padding: {
			left: 14px; } }

	&__message {
		color: #2a2727;
		font-size: 18px;
		line-height: 33px;
		font-weight: 300; }

	&__author {
		position: absolute;
		left: 0;
		bottom: 0;

		display: flex;
		align-items: center;
		width: 100%;
		height: 134px;
		padding-left: 40px;
		padding-right: 40px;

		background-color: $blue;
		color: #fff;
		box-sizing: border-box;

		@include media(1199px) {
			padding: {
				left: 11px;
				right: 10px; } } }

	&__photo {
		width: 72px;
		height: 72px;
		border-radius: 50%;
		overflow: hidden;

		img {
			max-width: 100%;
			max-height: 100%; }

		@include media(1199px) {
			width: 57px;
			height: 57px; } }

	&__name {
		padding-left: 34px;
		font-size: 20px;

		span {
			display: block;

			line-height: 1.7;

			&:nth-child(2) {
				font-size: 14px;
				font-weight: 300; } }

		@include media(1199px) {
			padding-left: 19px; } } }

.webp .testimonial-tile::before {
		background-image: url(../img/common/qoutes.webp); }
