.footer {
	position: relative;
	padding: {
		top: 8.722em;
		bottom: 6.944em;
		left: 11.5em;
		right: 3em; }

	background-color: #0e3d5e;
	color: #fff;
	font-size: 18px;

	@include media(1699px) {
		padding: {
			left: calc(8vw + 30px);
			bottom: 30px; } }

	@include media(1599px) {
		font-size: 16px; }

	@include media(1199px) {
		position: relative;
		padding: {
			top: 40px;
			left: 30px;
			right: 50px;
			bottom: 0; }

		font-size: 14px; }

	@include media(767px) {
		padding: {
			top: 448px;
			left: 0;
			right: 0; } }

	&__row {
		display: flex;
		justify-content: space-between;

		@include media(1399px) {
			flex-wrap: wrap; }

		@include media(1199px) {
			$offset: 294px + 30 + 27;
			max-width: calc(100% - #{$offset});
			padding-right: 27px; }

		@include media(869px) {
			max-width: calc(100% - 260px); }

		@include media(767px) {
			max-width: 100%;
			padding: {
				right: 0; } }

		&_bottom {
			padding-top: 10.39em;
			font-weight: 300;

			@include media(1699px) {
				padding-top: 150px; }

			@include media(1199px) {
				max-width: 100%;
				padding-top: 40px;
				padding-bottom: 40px;
				padding-right: 0;
				border-top: 1px solid #145482; }

			@include media(767px) {
				padding-top: 22px;
				padding-bottom: 30px; } }

		a {
			text-decoration: none;
			color: inherit;
			transition: color 0.1s;
			&:hover {
				color: $blue; } } }

	&__col {
		max-width: 18.56em;

		@include media(1399px) {
			max-width: 100%;
			&:nth-child(4) {
				order: 1;

				width: 100%; } }

		@include media(1199px) {
			&:nth-child(4) {
				display: flex;
				padding-bottom: 36px;
				border-top: 1px solid #145482; }

			&:last-child {
				position: absolute;
				right: 52px; } }

		@include media(949px) {
			flex-basis: 50%;

			&:nth-child(4) {
				width: auto;
				max-width: 50%;
				display: block;
				border-top: 0; } }

		@include media(767px) {
			flex-basis: 100%;
			padding: {
				bottom: 20px;
				left: 23px;
				right: 23px; }

			&:nth-child(4) {
				flex-basis: 100%;
				max-width: 100%;

				padding-top: 30px;
				padding-bottom: 30px;
				padding-left: 0;
				padding-right: 0;
				border-top: 1px solid #145482; }

			&:last-child {
				top: 0;
				left: 0;
				right: 0;

				height: 408px;
				padding: 16px;

				box-sizing: border-box;
				background-color: #fff; } } }

	&__title {
		margin-bottom: 2.6em;
		color: $blue;
		text-transform: uppercase;
		font-weight: 700; }

	&__nav {
		padding-right: 1.11em;

		li {
			margin-bottom: 2.6em; }
		a {
			white-space: nowrap; }

		@include media(767px) {
			padding-right: 0;

			columns: 2;

			li {
				margin-bottom: 30px; } } }

	&__contacts {
		li {
			position: relative;
			margin-bottom: 1.94em;
			padding-left: 2.67em;
			line-height: 1.44;

			svg {
				position: absolute;
				left: 0;
				top: 6px;
				color: $blue; } }


		.icon_placeholder {
			width: 1.11em;
			height: 1.11em; }

		.icon_tel {
			width: 1.278em;
			height: 1.278em; }

		.icon_envelope {
			width: 1.167em;
			height: 0.67em; }

		@include media(1399px) {
			display: flex;
			justify-content: space-between;
			align-items: center;
			margin-top: 2em;

			li {
				margin-bottom: 0;
				padding-left: 1.7em;

				svg {
					top: 0.73em;
					transform: translateY(-50%); } } }

		@include media(1199px) {
			flex-basis: 38%;
			display: block;
			margin-top: 27px;
			padding-right: 80px;

			li {
				margin: {
					bottom: 28px; }

				&:last-child {
					margin-bottom: 0; } } }

		@include media(949px) {
			margin-top: 0;
			padding-right: 0; }

		@include media(767px) {
			padding: {
				left: 23px;
				right: 23px; } } }

	&__socials {
		display: flex;
		align-items: flex-end;
		margin-top: 5.167em;
		padding-left: 0.44em;

		h2 {
			margin-right: 2.22em;
			font-size: 1.22em;
			font-weight: 500;
			white-space: nowrap; }
		ul {
			display: flex; }

		li {
			margin-right: 1.44em;
			color: $blue; }

		a {
			transition: color 0.3s;
			&:hover {
				color: #fff; } }

		.icon_instagram {
			width: 1.78em;
			height: 1.78em; }
		.icon_facebook {
			width: 0.89em;
			height: 1.67em; }

		@include media(1199px) {
			position: relative;

			flex-basis: 50%;
			align-items: flex-start;
			margin-top: 27px;
			padding-left: 0; }

		@include media(767px) {
			justify-content: center;
			align-items: flex-end;
			padding: {
				top: 30px;
				left: 23px;
				right: 23px; }
			border-top: 1px solid #145482; } }

	&__privacy {

		&_md {
			display: none; }

		a {
			margin-right: 3.33em;

			&:last-child {
				margin-right: 0; } }

		@include media(1199px) {
			font-weight: 300;

			&_md {
				position: absolute;
				left: 0;
				bottom: 0;
				display: block; }

			&_md-hidden {
				display: none; }

			a {
				display: block;
				margin-right: 0;
				margin-top: 20px; } }


		@include media(949px) {
			order: -1;
			width: 100%;
			text-align: center;

			&_md {
				display: none; }
			&_md-hidden {
				display: block; }

			a {
				margin-top: 0;
				margin-bottom: 20px; } } }

	&__copyrights {
		color: #ffffff;
		text-align: center;

		@include media(1199px) {
			width: 100%;
			opacity: 0.2; } } }

