@import "./layout/variables", "./layout/mixins", "./layout/fonts";

 :root {
	font-family: 'Poppins', sans-serif; }

.wrapper {
	overflow: hidden;
	color: $darkblue; }

main {
	position: relative; }

b, strong {
	font-weight: 800; }

.font-bold {
	font-weight: 800; }

.font-light {
	font-weight: 300; }

.font-poppins {
	font-family: Poppins; }

@each $color, $var in (blue: $blue, darkblue: $darkblue) {
	.color-#{$color} {
		color: $var; } }

.hidden-md {
	@include media(1199px) {
		display: none; } }

%default-block {
	padding: {
		top: (120em / 18);
		bottom: (150em / 18); }

	font-size: 18px;

	@media screen and (min-width: 768px) and (max-width: 1600px) {
		font-size: 16px; }

	@media screen and (min-width: 1200px) and (max-width: 1459px) {
		padding-left: 8vw; }

	@include media(767px) {
		padding: {
			top: 40px;
			bottom: 40px; } } }

