.we-help {
	padding: {
		top: (83em / 18); }

	background-color: $darkblue;
	color: white;

	font-size: 18px;

	@media screen and (min-width: 768px) and (max-width: 1600px) {
		font-size: 16px; }

	@media screen and (min-width: 1200px) and (max-width: 1459px) {
		padding-left: 8vw; }

	@include media(767px) {
		padding: {
			top: 40px;
			bottom: 40px; } }

	&__title {
		text-align: center; }

	&__list {
		display: flex;
		flex-wrap: wrap;
		margin-top: (125em / 18);

		@include media(767px) {
			margin-top: 40px; } }

	&__item {
		flex-basis: 50%;
		display: flex;
		margin-bottom: 3.8em;

		box-sizing: border-box;

		&:nth-child(odd) {
			padding: {
				right: 30px; }

			@include media(1199px) {
				padding-right: 15px; }

			@include media(767px) {
				padding-right: 0; } }

		&:nth-child(even) {
			padding: {
				left: 30px; }

			@include media(1199px) {
				padding-left: 15px; }

			@include media(767px) {
				padding-left: 0; } }

		@include media(767px) {
			flex-basis: 100%;
			margin-bottom: 1.5em; } }

	&__icon-wrapper {
		flex-shrink: 0;
		width: (80em / 18);
		margin-right: (78em / 16);

		@include media(1199px) {
			margin-right: 20px; }

		@include media(575px) {
			width: (40em / 18); } }

	&__icon {
		max-width: 100%;

		&_analytics {
			width: 81px; }
		&_barista {
			width: 80px; }
		&_give {
			width: 82px; }
		&_cofeemaker {
			width: 60px; } }

	&__text {
		font-weight: 300;
		line-height: (32 / 18);

		p {
			margin-bottom: 1em; } } }
