.header {
	position: relative;
	box-sizing: border-box;

	&_has-columns {
		color: white;

		@media screen and (min-width: 768px) and (max-width: 1200px) {
			color: $darkblue; }

		.burger__line {
			background-color: #fff; }

		.cart-link__counter {
			background-color: $darkblue;

			@media screen and (min-width: 1200px), screen and (max-width: 767px) {
				background-color: #fff;
				color: $darkblue; } }

		.cart-link__icon {
			color: currentColor; } }

	&_has-columns & {

		&__top-panel {
			position: absolute;
			top: 0;
			left: 0;
			right: 0;

			box-shadow: none;
			background-color: transparent;

			@media screen and (min-width: 1700px) {
				padding: {
					top: 57px;
					bottom: 57px;
					left: calc(50% - 693px); } }

			@media screen and (min-width: 1200px) and (max-width: 1699px) {
				padding: {
					left: calc(8vw + 30px);
					top: 37px;
					bottom: 37px; } }

			@media screen and (min-width: 768px) and (max-width: 1200px) {
				position: static;
				box-shadow: 0 1px 20px 6px rgba(14, 61, 94, 0.07);
				background-color: #ffffff; } }

		&__logo {
			display: none;

			@include media(1199px) {
				display: inline-block; } }

		&__auth {
			color: #fff;

			@media screen and (min-width: 768px) and (max-width: 1199px) {
				color: $darkblue; } } }

	&__top-panel {
		position: relative;
		z-index: 11;

		display: flex;
		align-items: center;
		justify-content: space-between;
		padding: {
			top: 30px;
			bottom: 30px;
			right: 45px;
			left: 45px; }

		box-shadow: 0 1px 20px 6px rgba(14, 61, 94, 0.07);
		background-color: #ffffff;
		transition: background-color 0.3s;

		@include media(1600px) {
			padding: {
				top: 10px;
				bottom: 10px; } }

		@include media(1199px) {
			padding: {
				top: 25px;
				bottom: 25px;
				left: 30px;
				right: 50px; } }

		@include media(767px) {
			padding: {
				left: 15px;
				right: 16px; }

			&_main,
			&.active {
				background-color: $darkblue; }

			&.active {

				@include media(767px) {

					.header__logo {
						opacity: 0;
						pointer-events: none; }

					.header__nav {
						opacity: 1;
						pointer-events: auto;

						ul {
							transform: scale(1); } }

					.header__auth {
						opacity: 1;
						pointer-events: auto;
						transform: translateX(-50%) scale(1); }

					.header__lang-switcher {
						opacity: 1;
						pointer-events: auto; }

					.burger__line {
						background-color: #fff; } } } } }

	&__logo {
		order: -1;
		flex-shrink: 0;
		display: inline-block;
		width: 53px;
		height: 90px;
		margin-right: 8.49vw;

		color: $blue;

		svg {
			max-width: 100%;
			max-height: 100%;
			min-height: 1px; }

		@include media(1600px) {
			height: 60px; }

		@include media(1199px) {
			margin-right: 0; }

		@include media(767px) {
			height: 45px;
			transition: opacity 0.3s; } }

	&__burger {
		display: none;

		@include media(767px) {
			display: block;
			order: -1;
			transform: translateX(-10px); } }

	&__nav {
		flex: 0 1;
		flex-basis: calc((50% - 210px) - (50% - 693px));
		max-width: calc((50% - 210px) - (50% - 693px));

		a {
			color: inherit; }

		@include media(1699px) {
			flex-basis: calc((50% - 210px));
			max-width: calc((50% - 210px)); }

		@include media(1199px) {
			flex-basis: auto;
			max-width: 100%;
			margin-left: calc(10vw - 42px);
			box-sizing: border-box; }

		@include media(767px) {
			position: absolute;
			left: 0;
			top: 100%;
			width: 100%;
			margin-left: 0;

			background-color: $darkblue;
			opacity: 0;
			pointer-events: none;

			transition-property: opacity, transform;
			transition-duration: 0.3s;
			transition-timing-function: ease;

			ul {
				transition: transform 0.3s ease-in-out;
				transform: scale(1.3); } } }

	&__lang-switcher.lang {
		margin: {
			right: auto;
			left: 100px; }
		color: $blue;

		@include media(1699px) {
			margin-left: 50px; }

		@include media(1199px) {
			margin-left: 0; }

		@include media(767px) {
			position: absolute;
			right: 50%;
			top: 29px;
			right: 0;
			margin: 0;

			transition: opacity 0.3s ease;
			transform: translateX(-70px);

			opacity: 0;
			pointer-events: none; } }

	&__cart {
		margin-right: 27px;
		margin-left: 34px;

		@include media(1199px) {
			margin-right: 0;
			margin-left: 10px; }

		@include media(767px) {
			margin-right: 10px; } }

	&__auth {
		display: flex;
		user-select: none;
		color: $darkblue;

		button,
		a {
			display: inline-block;
			margin-left: 43px;
			padding: 0;

			color: inherit;
			font-size: (18em / 16);
			font-weight: bold;
			text-transform: uppercase;

			white-space: nowrap;
			appearance: none;
			border: none;
			background: none;

			cursor: pointer;

			&:hover {
				color: $blue; }

			&:focus {
				outline: none; } }

		@media screen and (min-width: 768px) {
			button, a {
				font-size: 1em; } }

		@include media(1199px) {
			button, a {
				margin-left: 3.9vw; } }

		@include media(767px) {
			position: absolute;
			left: 50%;
			top: calc(100vh - 140px);

			display: block;

			text-align: center;
			opacity: 0;
			pointer-events: none;

			transform: translateX(-50%) scale(1.5);
			transition-property: opacity, transform;
			transition-duration: 0.3s;
			transition-timing-function: ease;

			button, a {
				display: block;
				margin-left: 0;
				margin-top: 16px;
				margin-left: auto;
				margin-right: auto;
				color: $blue; } }

		@media screen and (max-height: 559px) {
			top: calc(100vh - 100px); } }

	&__row {
		display: flex;

		@include media(767px) {
			flex-wrap: wrap; } }

	&__content {
		position: relative;

		flex: 0 0 50%;
		max-width: 50%;
		min-height: 100vh;
		padding: {
			top: 107px;
			left: calc(50% - 693px);
			bottom: 80px; }

		background-color: $darkblue;
		color: white;
		box-sizing: border-box;

		@include media(1699px) {
			padding: {
				left: calc(8vw + 30px); } }

		@include media(1199px) {
			min-height: calc(100vh - 110px);
			padding: {
				left: 30px;
				right: 30px;
				top: 60px; } }

		@include media(767px) {
			display: flex;
			flex-direction: column;
			flex-basis: 100%;
			max-width: 100%;
			min-height: 1px;
			padding: {
				left: 16px;
				right: 16px;
				top: 100px;
				bottom: 40px; }

			&_index {
				padding-bottom: 0; } }

		@media screen and (max-height: 479px) {
			padding: {
				top: 125px; } } }

	&__picture {
		position: relative;
		width: 50%;

		background-size: cover;
		background-repeat: no-repeat;


		// mask
		&::before {
			content: '';
			position: absolute;
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;

			// background-color: #0e3d5e
			opacity: 0.6;
			pointer-events: none; }

		@include media(767px) {
			width: 100%; }

		&:not(&_keep) {
			@include media(767px) {
				display: none;
				background-image: none !important; } }

		&_center {
			display: flex;
			justify-content: center;
			align-items: center;
			padding: {
				top: 2em;
				bottom: 2em; } } }

	&__iframe {
		position: relative;
		width: 100%;
		height: 100%;

		iframe {
			width: 100%;
			height: 100%;
			border: none; }

		&::after {
			content: '';
			position: absolute;
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
			z-index: 1;

			background-color: rgba($darkblue, 0.5);

			pointer-events: none; } }

	a {
		text-decoration: none; } }

@each $key, $value in $header-backgrounds {
	.header_#{$key} .header__picture {
		background-image: url($img-folder + $value + '.jpg'); }
	.webp .header_#{$key} .header__picture {
		background-image: url($img-folder + $value + '.webp'); } }
