.layout {
	position: fixed;
	top: 0;
	left: 0;
	bottom: 0;
	right: 0;
	z-index: 100;

	display: flex;
	justify-content: center;
	align-items: center;

	background-color: rgba(14, 61, 94, 0.95);

	opacity: 0;
	pointer-events: none;

	&__back {
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0; }

	&.visible {
		opacity: 1;
		pointer-events: auto;
		transition: opacity 0.3s ease-in-out; } }

.popup {
	display: none;
	position: relative;

	box-shadow: 0 0 16px 14px rgba(6, 32, 51, 0.09);
	background-color: #ffffff;

	&.visible {
		display: block;
		animation: fadeIn 0.5s ease-in-out both; }

	&__close {
		position: absolute;
		top: 10px;
		right: 10px;

		width: 20px;
		height: 20px;

		padding: 5px;

		background: none;
		border: none;
		color: $blue;
		appearance: none;
		line-height: 0;
		box-sizing: border-box;

		svg {
			width: 100%;
			height: 100%; }

		&_white {
			color: #fff; } } }

@keyframes fadeIn {
	from {
		opacity: 0;
		transform: translateY(5%); }

	to {
		opacity: 1;
		transform: translateY(0); } }

.account-popup {
	display: flex;
	min-height: 65vh;
	min-width: 37.5vw;
	max-width: 90vw;

	@include media(767px) {
		min-width: 0; }

	h2 {
		font-family: Mont;
		font-size: 30px;
		font-weight: 800;
		line-height: 1.2; }

	&__additional {
		flex: 0 1 40.43%;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		padding: {
			left: 40px;
			right: 40px; }

		text-align: center;
		color: #fff;

		background-size: cover;
		background-repeat: no-repeat;

		p {
			margin: {
				top: 17px; }
			line-height: 1.5; }

		button {
			margin: {
				top: 50px; } }

		@include media(767px) {
			display: none; } }

	&__main {
		flex: 0 1 (100% - 40.43);
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;

		padding: {
			top: 10vh;
			bottom: 10vh; }

		box-sizing: border-box;

		h2 {
			color: $blue; }

		p {
			max-width: 26.25em;
			margin-top: 12px;
			padding: {
				left: 10%;
				right: 10%; }
			color: $darkblue;
			line-height: 1.5;
			text-align: center; }

		&_recovery {
			flex-basis: 100%; }

		@include media(767px) {
			flex-basis: 100%;
			max-width: 100%;
			padding: {
				left: 30px;
				right: 30px; }

			p {
				padding: 0; } }

		@media screen and (max-height: 499px) {
			padding: {
				top: 6vh;
				bottom: 6vh; } } }

	&__recovery {
		flex-basis: 100%; }

	&__form {
		width: 360px;
		max-width: 90%;
		text-align: center;

		@include media(767px) {
			min-width: 283px;
			max-width: 100%; }

		@include media(575px) {
			min-width: 0; } }

	&__recovery {
		margin: {
			bottom: 70px; }
		padding: 0;
		color: $blue;
		font-family: Poppins;
		font-size: 14px;
		line-height: 24px;
		background: none;
		border: none;
		border-bottom: 2px solid $blue;


		appearance: none; } }


.account-popup__additional {
	background-image: url(../img/common/popup-login-bg.jpg); }

.webp .account-popup__additional {
	background-image: url(../img/common/popup-login-bg.webp); }


.form {
	text-align: center;

	&__field {
		display: block;
		margin: 50px 0;

		@include media(575px) {
			margin: 40px 0; }

		@media screen and (max-height: 499px) {
			margin: 30px 0; } } }

.field {
	position: relative;
	width: 100%;

	&__icon {
		position: absolute;
		left: 0;
		bottom: 6px;

		&_user {
			width: 13px;
			height: 14px; }

		&_envelope {
			width: 14px;
			height: 11px; }

		&_padlock {
			width: 10px;
			height: 13px; }

		&_phone-call {
			width: 11px;
			height: 11px; }

		&_comment {
			width: 12px;
			height: 12px; } }


	&__input {
		width: 100%;
		height: 1.5em;
		padding: {
			left: 21px; }

		background: none;
		border: none;
		color: $darkblue;
		font-size: 1em;
		line-height: 1;
		border-bottom: 1px solid #dfe7eb;

		box-sizing: border-box;

		&:focus {
			outline: none; }

		&::placeholder {
			color: inherit;
			font-size: 12px;
			line-height: 1; } } }

