.company-tile {
	display: block;
	width: (220em / 18);
	padding: {
		top: (50em / 18);
		left: 15px;
		right: 15px;
		bottom: (60em / 18); }

	box-sizing: border-box;

	font-size: 1em;

	box-shadow: 0 2px 11px 8px rgba(79, 156, 194, 0.05);
	background-color: #ffffff;
	color: inherit;
	text-decoration: none;
	text-align: center;

	transition-property: color, background-color;
	transition-duration: 0.2s;

	&:hover {
		background-color: $blue;
		color: #fff; }

	&__icon-wrapper {
		display: flex;
		justify-content: center;
		align-items: center;
		height: (165em / 18); }

	&__icon {

		&_bb-cafe {
			width: (120em / 18);
			height: (120em / 18); }

		&_bb-karaoke {
			width: (110em / 18);
			height: (110em / 18); }
		&_bb-roastery {
			width: (71em / 18);
			height: (151em / 18); }
		&_bb-karaoke-rooms {
			width: (107em / 18);
			height: (107em / 18); }
		&_bb-karaoke-paty-bus {
			width: (108em / 18);
			height: (114em / 18); }

		svg {
			max-width: 100%;
			max-height: 100%;
			min-height: 1px; } }

	&__title {
		h4 {
			margin-top: 7px;
			font-size:  (17em / 18);
			line-height: (20 / 17);
			text-transform: uppercase; } }

	&__link {
		display: inline-block;
		margin-top: (80em / 18);
		font-size: (14em / 18);
		font-weight: 500; }

	&__bluebird {
		width: 79px;
		height: 11px; } }

