.btn-media {
	position: relative;
	display: flex;
	justify-content: center;
	align-items: center;
	width: 267px;
	height: 267px;
	border-radius: 50%;
	cursor: pointer;
	color: white;

	@include media(1199px) {
		width: 200px;
		height: 200px; }

	&:focus {
		outline: none; }

	&:hover & {

		&__outer {
			transform: rotate(0) scale(1);

			&::before {
				transform: scale(1);
				opacity: 1; } } }

	&__outer {
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;

		padding: 20px;

		border-radius: 50%;

		background-color: rgba(#fff, 0.2);
		transform: rotate(45deg) scale(0.7);

		transition: transform 0.2s;

		&::before {
			content: '';
			display: inline-block;
			width: 100%;
			height: 100%;
			background-image: url('../img/common/btn-media-text.png');
			background-size: 64% auto;
			background-repeat: no-repeat;
			transform: scale(0.7);
			opacity: 0;
			transition: transform 0.2s, opacity 0.2s; } }

	&__button {
		position: absolute;
		width: 60px;
		height: 60px;

		border-radius: 50%;
		box-sizing: border-box; } }


.webp .btn-media__outer::before {
	background-image: url('../img/common/btn-media-text.webp'); }
