.more-tutorials {
	padding: {
		top: (116em / 18);
		bottom: (150em / 18); }

	background-color: #fff;

	font-size: 18px;

	@media screen and (min-width: 1200px) and (max-width: 1600px) {
		font-size: 16px; }

	@include media(767px) {
		padding: {
			top: 40px;
			bottom: 40px; } }

	&__list {
		padding-top: 70px;

		@include media(767px) {
			padding: {
				top: 40px; } }

		.tutorial-tile {

			@include media(767px) {
				width: 270px; } } } }
