.catalog {
	padding: {
		top: 1em;
		bottom: 7.5em; }

	&__category {
		position: relative;

		padding: {
			top: 10px;
			bottom: 10px; } }

	&__results {
		color: $darkblue;
		font-size: 1.875em;
		font-weight: 300;
		line-height: 1.47;

		@include media(991px) {
			position: absolute;
			opacity: 0;
			transition: opacity 0.3s;

			&.visible {
				opacity: 1; } } }

	&__mob-btns {
		display: none;

		@include media(991px) {
			position: relative;
			z-index: 1;

			display: inline-flex; } }

	&__menu-trigger {
		margin-right: 10px;
		padding: 3px 25px;

		border-radius: 35px;
		background-color: rgba($blue, 0.2);
		line-height: 2.75;
		font-size: 1.25em;
		color: $blue;

		appearance: none;
		border: none; }

	&__search {
		position: absolute;
		left: 0;
		top: 0;
		right: 0;
		bottom: 0;

		.search__field {
			transform: scaleX(0);
			transform-origin: 100% 50%;
			transition: transform 0.3s; }

		&.active {
			z-index: 2;
			.search__field {
				transform: scaleX(1); } } }

	&__search-trigger {
		position: absolute;
		right: 0;
		top: 20px;
		bottom: 20px;

		padding: 0 7px;

		svg {
			width: 18px;
			height: 100%; }

		border: none;

		background: none;
		appearance: none;
		transition: box-shadow 0.3s;

		&:focus {
			outline: none;
			box-shadow: 0 0 0 2px #f6fafc, 0 0 2px 4px $blue; } }

	&__list {
		display: flex;
		flex-wrap: wrap;
		padding: {
			top: (40em / 18); }

		font-size: (18em / 16);

		@include media(575px) {
			font-size: 14px; } }

	&__item {
		flex-basis: calc(33.3333% - 17px);
		max-width: calc(33.3333% - 17px);

		margin: {
			right: 25px;
			bottom: 25px; }

		box-sizing: border-box;

		@media screen and (min-width: 1200px) {
			&:nth-child(3n + 3) {
				margin-right: 0; } }

		@include media(1199px) {
			flex-basis: calc(50% - 10px);
			max-width: calc(50% - 10px);
			margin: {
				right: 20px;
				bottom: 20px; }

			&:nth-child(even) {
				margin-right: 0; } }

		@include media(767px) {
			flex-basis: calc(50% - 5px);
			max-width: calc(50% - 5px);
			margin-right: 10px;
			margin-bottom: 10px; } }

	&__show-more {
		margin-top: (73em / 16);

		font-size: 16px;

		text-align: center; }

	&__pagination {
		margin-top: 64px; } }

.search {

	&__field {
		position: relative;
		z-index: 1;

		display: flex;
		align-items: center;
		width: 100%;
		height: 100%;
		padding: 20px;

		box-sizing: border-box;
		background-color: #ffffff;

		font-size: 18px;
		font-weight: 400;
		color: $darkblue;

		input {
			flex: 1 1 100%;
			height: 100%;
			padding: 0 10px;
			border: none;

			font-family: inherit;
			font-size: inherit;

			caret-color: $blue;

			&:focus {
				outline: none; } } }

	&__button {

		border: none;

		background: none;
		appearance: none;
		transition: box-shadow 0.3s;

		&:focus {
			outline: none;
			box-shadow: 0 0 0 2px #f6fafc, 0 0 2px 4px $blue; }

		&_submit {
			height: 100%;
			margin-right: 30px;

			svg {
				width: 25px;
				height: 25px; } } } }




.result {
	display: inline-flex;
	align-items: center;
	font-weight: bold;

	&__icon {
		display: flex;
		justify-content: center;
		align-items: center;
		width: 17px;
		height: 17px;
		margin-left: 15px;

		border-radius: 50%;
		background-color: $blue;
		color: #fff;

		svg {
			width: 8px;
			height: 5px; } } }


.autocomplete {
	position: relative;

	width: 100%;
	height: 100%;

	&__field {
		position: relative;
		z-index: 1;
		width: 100%;
		height: 100%;
		background-color: transparent;

		&_suggestion {
			position: absolute;
			top: 0;
			left: 0;
			z-index: 0;

			color: #dfe7eb; } } }
