.goods {
	padding: {
		top: 75px;
		bottom: 70px; }

	&_white {
		background-color: #fff; }

	&_lightgray {
		background-color: #f6fafc; }

	&_scroll-panel {

		@media screen and (min-width: 1200px) and (max-width: 1459px) {
			padding-left: 8em; }

		@media screen and (min-width: 1570px) and (max-width: 1649px) {
			.goods__container {
				max-width: 1100px; } }

		@media screen and (min-width: 1200px) and (max-width: 1569px) {
			.goods__container {
				max-width: 970px; } } }

	@include media(1600px) {
		padding: {
			bottom: 40px; } }

	@include media(1199px) {
		padding: {
			top: 50px;
			bottom: 50px; } }

	@include media(767px) {
		padding: {
			top: 45px;
			bottom: 0; } }

	&__container {
		position: relative; }

	&__title {
		color: $blue;
		font-size: 30px;
		line-height: 1.875em;
		line-height: 1.67; }

	&__slider {
		margin: {
			top: 50px;
			bottom: -40px; }
		padding: {
			bottom: 120px; }

		.swiper-slide {
			padding: {
				top: 15px;
				bottom: 15px;
				left: 12px;
				right: 12px; }

			box-sizing: border-box; }

		@include media(1299px) {
			margin: {
				top: 30px;
				bottom: -56px; }
			padding: {
				bottom: 136px; } }


		@include media(959px) {
			.product-tile__buttons button {
				font-size: 8px; } }

		@include media(575px) {
			margin: {
				bottom: -54px; }
			padding: {
				bottom: 114px; }
			font-size: 14px;

			.swiper-slide {
				padding: 4px; }

			.product-tile__name {
				line-height: 1.143; } } }

	&__control {
		position: absolute;
		top: 45%;

		transform: translateY(-50%);

		&_prev {
			right: 100%;
			transform: translateY(-50%) scale(-1); }

		&_next {
			left: 100%; }


		@include media(1399px) {
			display: none; } }

	&__pagination {
		z-index: 0;
		bottom: 90px!important;
		display: none;

		@include media(1399px) {
			display: block; }

		@include media(1199px) {
			bottom: 90px!important; }

		@include media(575px) {
			bottom: 40px!important; } }

	&__load-more {
		text-align: center;

		@include media(767px) {
			margin-bottom: 2em;
			transform: none; } } }


