.bb-floating {
	position: fixed;
	bottom: 93px;
	right: 93px;
	z-index: 50;

	@include media(575px) {
		bottom: 20px;
		right: 50%;
		transform: translateX(50%); }

	&__button {
		display: block;
		width: 80px;
		height: 80px;
		border: none;

		padding: 0;

		border-radius: 50%;

		color: #fff;
		text-decoration: none;

		&:focus {
			outline: none;

			span {
				box-shadow: 0 0 0 1px #fff, 0 0 15px 2px $blue; } }

		&.active,
		&:hover {
			span {
				background-color: $darkblue;
				box-shadow: 0 0 0 2px #fff; } }

		span {
			position: relative;

			display: inline-flex;
			justify-content: center;
			align-items: center;
			width: 80px;
			height: 80px;
			padding: {
				top: 0;
				bottom: 0;
				left: 10px;
				right: 10px; }

			box-shadow: 0 0 6px rgba(14, 61, 94, 0.2);
			border-radius: 50%;
			background-color: #64b2db;
			box-sizing: border-box;
			text-align: center;

			transition-property: background-color, box-shadow;
			transition-duration: 0.3s; }


		&::after {
			@extend %bluebox-rings;
			width: 140px;
			height: 140px;
			animation-delay: 0.5s; }

		&::before {
			@extend %bluebox-rings;
			width: 164px;
			height: 164px; }

		@include media(575px) {
			box-shadow: 0 0 15px rgba(14, 61, 94, 0.31);

			&::before, &::after {
				display: none; } } } }


%bluebox-rings {
	content: '';

	position: absolute;
	top: 50%;
	left: 50%;
	z-index: -2;

	transform: translate(-50%, -50%);

	background-color: #fff;
	border-radius: 50%;

	animation-name: pulsing;
	animation-duration: 3s;
	animation-timing-function: linear;
	animation-iteration-count: infinite; }

@keyframes pulsing {
	0% {
		transform: translate(-50%, -50%) scale(0);
		opacity: 0.8; }
	30% {
		transform: translate(-50%, -50%) scale(0.5); }
	50% {
		opacity: 0.8; }
	100% {
		transform: translate(-50%, -50%) scale(1);
		opacity: 0; } }
