.pagination {
	display: flex;
	// align-items: center
	justify-content: center;
	color: $darkblue;
	font-weight: 400;
	line-height: 23px;

	a {
		display: flex;
		align-items: center;
		justify-content: center;

		height: 100%;

		color: inherit;
		text-decoration: none;

		&:hover {
			color: $blue; } }

	&__item {

		&_number {
			margin: {
				right: 7px; }
			padding: {
				right: 7px;
				left: 2px; }

			border-radius: 1px; }

		&_current {
			background-color: $blue;
			color: #fff;

			a:hover {
				color: #fff;
				cursor: default; } }

		&_prev,
		&_next {
			svg {
				width: 10px;
				height: 6px; } }

		&_prev {
			margin-right: 30px;
			svg {
				transform: rotate(90deg); } }

		&_next {
			margin-left: 23px;
			svg {
				transform: rotate(-90deg); } } } }
