.cart {
	padding: {
		top: 40px;
		bottom: 150px; }

	font-size: 14px;

	@include media(767px) {
		padding: {
			top: 21px;
			bottom: 40px; } }

	&__mob-btns {
		@extend .catalog__mob-btns;
		margin-bottom: 20px; }

	&__menu-trigger {
		@extend .catalog__menu-trigger; }

	&__table {
		padding: {
			left: 20px;
			right: 20px; }

		background-color: #ffffff; }

	&__row {
		display: flex;

		color: $darkblue;

		border-bottom: 1px solid #f6fafc;

		&_header {
			padding: {
				top: 20px;
				bottom: 20px; }

			color: $blue;

			font-weight: 600;

			@include media(767px) {
				display: none; } }


		&_product {
			padding: {
				top: 20px;
				bottom: 20px; } }

		&_total {
			justify-content: space-between;
			padding: {
				top: 30px;
				bottom: 70px; }

			border-bottom: none;

			@include media(767px) {
				flex-wrap: wrap;
				padding: {
					top: 20px;
					bottom: 60px; } } } }

	&__col {
		display: flex;
		align-items: center;
		flex-wrap: wrap;
		box-sizing: border-box;

		$cat-basis: 46%;
		$rm-basis: 80px;
		$img-basis: 120px;

		&_cat {
			flex-basis: $cat-basis;
			padding: {
				left: 80px; }

			@include media(1199px) {
				flex-basis: 53%;
				padding-left: 50px; }

			@include media(767px) {
				display: none; } }

		&_total {
			flex: 1 1 auto;
			justify-content: flex-end;
			padding: {
				right: 60px; }

			@include media(1199px) {
				padding: {
					right: 0; } }

			@include media(767px) {
				display: none; } }

		&_remove {
			flex-basis: $rm-basis;
			justify-content: center;

			@include media(1199px) {
				flex-basis: 50px; }

			@include media(767px) {
				display: none; } }

		&_image {
			flex-basis: $img-basis;
			max-width: $img-basis;
			max-height: 165px;

			picture {
				display: inline-block;
				max-height: 100%;
				max-width: 100%; }

			img {
				max-height: 165px;
				max-width: 100%;
				min-height: 1px;
				width: auto;
				height: auto; } }

		&_info {
			$sum-prev-elems: $img-basis + $rm-basis;
			flex-basis: calc(#{$cat-basis} - #{$sum-prev-elems});
			padding: {
				left: 55px;
				right: 15px; }

			box-sizing: border-box;

			@include media(1199px) {
				flex-basis: calc(53% - 170px);
				padding: {
					left: 20px; } }

			@include media(767px) {
				flex: 1 1 auto;
				align-items: flex-start; }

			@include media(575px) {
				padding: {
					right: 0; } } }

		&_price {
			flex-basis: 13.68%;
			justify-content: center;

			@include media(1199px) {
				flex-basis: 16%; }

			@include media(767px) {
				display: none; } }

		&_quantity {
			flex-basis: 22%;
			max-width: 22%;
			justify-content: center;

			@include media(1199px) {
				flex-basis: 15%;
				max-width: 15%;
				min-width: 92px; }

			@include media(767px) {
				display: none; } } }

	&__product-title {
		font-weight: 600;
		font-size: 21px;
		line-height: 30px;
		letter-spacing: 2.52px;
		text-transform: uppercase; }

	&__product-desc {
		font-size: 14px;
		font-weight: 300;
		line-height: 22px;

		@include media(767px) {
			max-width: 17em; }

		@include media(575px) {
			font-size: 12px; } }

	&__product-mob-price {
		display: none;
		color: $blue;
		font-size: 16px;
		font-weight: 700;
		line-height: 27px;

		@include media(767px) {
			display: block; } }

	&__product-price {
		font-size: 20px;
		text-transform: uppercase;

		font-weight: 600;
		white-space: nowrap;

		@include media(767px) {
			display: none; } }

	&__remove-btn {

		width: 36px;
		height: 36px;

		padding: 10px;

		box-sizing: border-box;

		color: $blue;

		appearance: none;
		border: none;
		background: none;

		svg {
			height: 100%;
			width: 100%; } }

	&__coupon {
		padding-left: 80px;

		@include media(1199px) {
			padding: {
				left: 0; } }

		@include media(767px) {
			flex-basis: 100%; } }

	&__total {
		padding-right: 60px;

		@include media(1199px) {
			padding: {
				right: 0; } }

		@include media(767px) {
			flex-basis: 100%; } }

	&__go-shopping {
		min-width: 203px;

		@include media(575px) {
			width: 100%; } }

	&__msg {
		margin-top: 52px;

		@include media(767px) {
			margin-top: 0; } } }


.input-number {
	position: relative;
	width: 92px;
	height: 50px;

	border-radius: 5px;
	border: 1px solid #cfd8df;

	color: $darkblue;

	box-sizing: border-box;

	&__value {
		width: 100%;
		height: 100%;
		padding: {
			right: 23px; }

		line-height: 50px;
		text-align: center;
		appearance: none;
		background: none;
		border: none;
		box-sizing: border-box;

		font-family: Poppins;
		font-size: 18px; }

	&__controls {
		position: absolute;
		right: 0;
		top: 0;

		display: flex;
		flex-direction: column;
		height: 100%; }

	&__button {
		width: 22px;
		height: 24px;
		padding: 0;

		appearance: none;
		border: none;
		background: none;
		border-left: 1px solid #cfd8df;

		font-size: 16px;

		color: #cfd8df;

		&:first-child {
			border-bottom: 1px solid #cfd8df; }

		&:hover {
			color: $darkblue; }

		&:active {
			box-shadow: inset 0 0 2px rgba(black, 0.7); } } }


.coupon {
	position: relative;

	width: 315px;
	height: 60px;

	border-radius: 30px;
	border: 1px solid #cfd8df;

	@include media(575px) {
		width: 100%;
		height: 40px; }

	&__input {
		width: 100%;
		height: 100%;

		padding: {
			top: 19px;
			bottom: 19px;
			left: 29px; }

		background: none;
		border: none;
		box-sizing: border-box;

		font-size: 14px;
		font-weight: 300;
		line-height: 22px;

		&:focus {
			outline: none; }

		@include media(575px) {
			font-size: 12px; } }

	&__button {
		position: absolute;
		right: 0;
		top: 0;

		min-width: 136px;
		height: 100%;
		border-radius: 30px;

		@include media(575px) {
			padding: {
				top: 9px;
				bottom: 10px; }
			line-height: 1;
			font-size: 14px; } } }

.total {
	padding: {
		top: 14px; }

	font-size: 20px;

	@include media(1600px) {
		font-size: 16px; }

	@include media(767px) {
		padding: {
			top: 20px; } }

	&__container {
		@include media(767px) {
			padding: 15px 20px;
			background-color: #f4f9fb; } }

	&__row {
		display: flex;
		justify-content: space-between;
		align-items: center;
		margin-bottom: 16px;

		font-weight: 500;
		text-transform: uppercase;
		line-height: 1.7;

		span {
			font-weight: 600;
			color: $blue;
			font-size: 1.2em; }

		@include media(575px) {
			font-size: 16px; } }

	&__button {
		margin-top: 40px;

		@include media(575px) {
			width: 100%; } } }

.msg {
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;

	padding: {
		top: 3.75em;
		bottom: 3.75em; }

	background-color: #ffffff;

	text-align: center;
	font-size: 40px;
	color: $darkblue;
	font-family: "Mont";

	font-weight: 800;
	line-height: 1.25;

	@include media(767px) {
		font-size: 24px; }

	&__icon {
		max-width: 100px;
		max-height: 100px;

		@include media(767px) {
			max-width: 60px;
			max-height: 60px; } }

	&__text {
		max-width: 20em;
		margin-top: 10px;

		@include media(575px) {
			max-width: 90%; } } }


