.input-radio {
	display: inline-block;
	position: relative;

	&__real {
		position: absolute;
		top: 50%;
		left: 8px;

		width: 1px;
		height: 1px;
		padding: 0;

		overflow: hidden;
		appearance: none;

		&:focus {
			outline: none;

			& + .input-radio__label::before {
				box-shadow: 0 0 0 2px white, 0 0 0 4px rgba($blue, 0.5); } }

		&:checked + .input-radio__label::before {
			background-color: $blue; } }

	&__label {
		display: inline-flex;
		align-items: center;

		&::before {
			content: '';

			display: inline-block;
			width: 16px;
			height: 16px;
			margin-right: 28px;

			border: 3px solid rgba($blue, 0.2);
			box-sizing: border-box;
			background-clip: content-box;
			border-radius: 50%;
			background-color: rgba($blue, 0.2);

			transition: box-shadow 0.3s;

			@include media(575px) {
				margin-right: 16px; } } } }

