.our-cafe-subscribe {
	padding: {
		top: (130em / 18);
		bottom: (150em / 18); }

	font-size: 18px;
	text-align: center;

	@media screen and (min-width: 768px) and (max-width: 1600px) {
		font-size: 16px; }

	@media screen and (min-width: 1200px) and (max-width: 1459px) {
		padding-left: 138px; }

	@include media(767px) {
		padding: {
			top: 40px;
			bottom: 40px; } }

	&__button {
		margin-top: (55em / 16);
		font-size: (16em / 18); } }
