.product-details {
	&__title {
		color: #64b2db;
		font-size: 1.875em;
		line-height: 1.67;

		border-bottom: 2px solid #d5dde1;

		span {
			position: relative;

			display: inline-block;

			&::after {
				content: '';

				position: absolute;
				left: 0;
				top: 100%;

				width: 100%;
				height: 5px;

				transform: translateY(-1px);
				background-color: $blue; } }

		@include media(767px) {
			font-size: 1.25em; } }

	&__list {
		padding: {
			top: 64px; }

		columns: 2;

		@include media(767px) {
			padding: {
				top: 30px; }

			columns: 1; } }

	&__list-item {
		display: flex;

		margin: {
			bottom: 20px; }

		@include media(767px) {
			margin: {
				bottom: 15px; } } }

	&__icon {
		flex: 0 0 57px;
		color: $blue;

		svg {
			max: {
				width: 30px;
				height: 30px; } } }

	&__text {
		padding: {
			right: 40px; }

		font-size: 1.125em;
		font-weight: 300;
		line-height: 1.5;

		h3 {
			font-weight: 800; }

		@include media(575px) {
			padding: {
				right: 0; }
			font-size: 1em; } } }

