.lang {
	position: relative;

	width: 77px;

	text-align: center;
	color: $blue;
	background-color: transparent;

	&__trigger {
		position: relative;

		width: 100%;
		padding: {
			top: 6px;
			bottom: 7px;
			left: 5px;
			right: 5px; }
		border: none;

		box-sizing: border-box;
		line-height: 25px;
		background: none;
		color: inherit;
		font-family: inherit;
		font-size: inherit;
		font-weight: 500;

		appearance: none;
		text-align: center;
		cursor: pointer;
		white-space: nowrap;

		-webkit-tap-highlight-color: transparent;

		svg {
			width: 9px;
			height: 6px;
			margin-left: 4px;
			vertical-align: middle; } }

	&__list {
		position: absolute;
		top: 0;
		left: 0;

		width: 100%;

		line-height: 25px;

		border-radius: 3px;
		box-shadow: 0 1px 4px rgba(9, 48, 76, 0.09);
		opacity: 0;
		pointer-events: none;

		transition: opacity 0.1s ease-in-out;

		&.visible {
			opacity: 1;
			pointer-events: auto; } }

	&__current {
		width: 100%;
		border-bottom: 1px solid $blue;

		svg {
			width: 9px;
			height: 6px;
			margin-left: 4px;
			vertical-align: middle;
			transform: scale(-1); } }

	&__option {
		width: 100%;

		&:first-child {
			margin-top: 5px; }

		&:nth-child(2) {
			padding-top: 7px; }

		&:last-child {
			margin-bottom: 7px; }

		a {
			display: inline-block;
			width: 100%;
			color: inherit; } }

	&_main &__list {
			background-color: #fff;

			@include media(1199px) {
				background-color: $darkblue; }

			@include media(767px) {
				background-color: #fff; } }

	&_inner &__list {
			background-color: $darkblue; } }

@keyframes fadeIn {
	from {
		opacity: 0; }
	to {
		opacity: 1; } }
