.tutorial-card {

	&__row {
		display: flex;
		flex-wrap: wrap; }

	&__col {
		flex-basis: calc(50% - 15px);
		max-width: calc(50% - 15px);

		&:nth-child(odd) {
			margin-right: 30px; }

		@include media(767px) {
			flex-basis: 100%;
			max-width: 100%;
			margin-bottom: 20px;

			&:nth-child(odd) {
				margin-right: 0; } }

		@include media(575px) {
			margin-bottom: 10px; } }

	&__header {
		padding-bottom: 2em; }

	&__info {
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		height: 100%;
		padding: 15px 3.2em 40px 1.94em;

		font-size: 1.11em;
		line-height: 1.5;

		background: rgba($blue, 0.3);

		box-sizing: border-box;

		@include media(1199px) {
			font-size: calc(1vw + 6px); }

		@include media(767px) {
			font-size: 16px;
			padding: 15px 20px 30px; }

		h3 {
			margin-bottom: 0.3em;

			font-family: "Mont";
			font-size: 1.5em;
			font-weight: 800;
			line-height: 2.1; }

		.tutorial-card__list {
			margin-top: 1.3em;
			columns: 2;

			li {
				margin-bottom: 1.2em; }

			@include media(399px) {
				columns: 1; } }

		.tutorial-card__row {
			flex-wrap: nowrap;
			align-items: baseline; }

		a {
			font-weight: 600;
			color: inherit;
			text-decoration: none; } }

	&__icon {
		width: 19px;
		max-height: 17px;
		margin-right: 18px;

		@include media(364px) {
			display: none; } }

	&__title {
		margin: {
			top: 1.42em;
			bottom: 1.42em; }
		color: $blue; }

	&__step {
		padding: {
			top: 2em;
			bottom: 3.8em; }
		border-top: 1px solid #dfe7eb;

		@include media(575px) {
			padding: {
				top: 1em;
				bottom: 2em; } }

		h3 {
			margin-bottom: 0.5em;
			line-height: 2.066em; }

		p {
			margin-bottom: 2em;

			@include media(575px) {
				margin-bottom: 1em; } }

		&:last-of-type {
			padding-bottom: 50px; } }

	&__footer {
		text-align: center;

		h4 {
			margin-bottom: 30px;
			font-size: 1.94em;
			font-weight: bold;
			line-height: 1.71; } }

	p {
		padding-right: 10%;
		line-height: 2;

		@include media(767px) {
			padding-right: 0; } }

	img {
		width: 100%; } }
