.card-details {
	display: flex;
	justify-content: space-between;
	padding: {
		top: (24em / 16); }


	@include media(575px) {
		flex-wrap: wrap; }

	&__item {
		margin: {
			right: (40em / 16); }

		&_num {
			flex: 1 1 448px;
			min-width: (195em / 16);

			@include media(767px) {
				flex-basis: 60%; }

			@include media(575px) {
				flex-basis: 100%;
				margin-bottom: (25em / 16);
				margin-right: 0; } }

		&_expiry {
			flex: 1 1 215px;

			@include media(767px) {
				flex-basis: 20%; } }

		&_cvv {
			flex: 1 1 188px;

			@include media(767px) {
				flex-basis: 20%; }

			@include media(575px) {
				margin-right: 0; } } } }
