.subscribe-card {
	position: relative;
	width: 16.7em;
	height: 25em;
	padding: 1.5em;

	background-color: #5ca9d2;
	background-image: url(../img/common/footer-card-background.svg);
	background-position: right center;
	background-size: auto 100%;
	background-repeat: no-repeat;

	font-size: 20px;

	box-sizing: border-box;

	@include media(1599px) {
		font-size: 16px; }

	@include media(1199px) {
		width: 294px;
		height: 500px;

		font-size: 20px; }

	@include media(869px) {
		width: 230px; }

	@include media(767px) {
		width: 100%;
		height: 100%; }

	&__icon {
		width: 2.45em;
		height: 2.45em; }

	&__title {
		margin-top: 2.5em;

		font-size: 1.1em;
		line-height: 1.363;

		@include media(1199px) {
			font-size: 22px;
			font-weight: bold; }

		@include media(767px) {
			margin-top: 30px;
			font-size: 22px;
			line-height: 30px; } }

	&__email {
		width: 100%;
		margin-top: 2.5em;
		padding: {
			left: 0;
			right: 0;
			top: 6px;
			bottom: 6px; }

		border: none;
		background: none;
		border-bottom: 2px solid #fff;

		font-family: Poppins;
		font-size: 0.875em;
		font-weight: 400;
		line-height: 1.5em;

		appearance: none;

		&::placeholder {
			font-family: inherit;
			font-size: inherit;
			line-height: 1.5em;
			color: #fff; }

		@include media(1199px) {
			font-size: 14px; } }

	&__button {
		position: absolute;
		bottom: 30px;
		left: 30px;

		min-width: 126px;
		padding: {
			left: 0;
			right: 0; }

		@include media(1199px) {
			font-size: 14px; } } }
