.calendar {
	border: 1px solid #cdd8df;
	background-color: #f6fafc;

	box-sizing: border-box;

	@include media(767px) {
		border: none;

		background: none; }

	&__row {
		display: flex;
		flex-wrap: wrap;

		&_week {
			background-color: $blue;
			color: #fff;

			@include media(767px) {
				border-radius: 3px; } }

		&_days {
			@include media(767px) {
				margin-top: 15px; } } }

	&__cell {
		flex-basis: (100% / 7);
		max-width: (100% / 7);

		box-sizing: border-box;

		border-right: 1px solid #cdd8df;
		border-bottom: 1px solid #cdd8df;

		&:nth-child(7n + 7) {
			border-right: none; }

		&:nth-child(n + 29) {
			border-bottom: none; }

		@include media(767px) {
			border: none; }

		&_day {
			padding: {
				top: (20em / 18);
				bottom: (20em / 18); }

			text-align: center;
			white-space: nowrap;

			@include media(767px) {
				padding: {
					top: 5px;
					bottom: 5px; }

				font-size: 12px;

				span {
					display: none; } } }

		&_date {
			position: relative;
			overflow: hidden;

			&::before {
				content: '';
				float: left;
				width: 100%;
				padding-bottom: 100%; } }

		&_has-event {
			position: relative;

			&::after {
				content: '';
				position: absolute;
				right: 8px;
				top: 10px;

				width: 7px;
				height: 7px;

				border-radius: 50%;
				background-color: $blue;

				@include media(767px) {
					top: 5%;
					right: 5%;
					width: 6px;
					height: 6px; } } }

		@include media(767px) {
			.calendar__event {
				display: none; } } }

	&__content-wrapper {
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;

		display: flex;
		flex-direction: column;
		justify-content: space-between;
		padding: (20em / 18);

		@include media(1199px) {
			padding: {
				left: (10em / 18);
				bottom: (10em / 18); } }

		@include media(767px) {
			justify-content: center;
			align-items: center;
			padding: 2px; } }

	&__date {
		font-size: (23em / 18);

		text-align: right;

		@include media(1199px) {
			font-size: (20em / 18); }

		@include media(767px) {
			display: flex;
			justify-content: center;
			align-items: center;
			width: (34em / 18);
			height: (34em / 18);

			font-size: 1em;

			border-radius: 50%;

			&_active {
				background-color: $blue;
				color: #fff; } } }

	&__event {
		font-size: (14em / 18);
		font-weight: 300;
		line-height: (20 / 14);

		color: $blue;

		@include media(767px) {
			font-size: 18px;
			line-height: (26 / 18); } }

	&__time {
		font-weight: 700; }

	&__name {} }

