.sidebar-form {
	padding: {
		top: (50em / 18);
		left: (40em / 18);
		right: (40em / 18); }

	text-align: center;

	&_bb {
		@include media(575px) {
			padding: {
				left: 0;
				right: 0; } } }

	&__title {
		font-weight: 800;
		text-transform: uppercase;
		text-align: left; }

	&__content {
		padding: {
			top: (20em / 18); } } }
