.offer-by-category {
	flex-basis: calc(25% - 22.5px);
	max-width: calc(25% - 22.5px);

	@include media(1199px) {
		flex-basis: calc(25% - 10px);
		max-width: calc(25% - 10px); }

	@include media(767px) {
		flex-basis: calc(50% - 10px);
		max-width: calc(50% - 10px);
		margin-bottom: 20px; }

	&__header {
		display: flex;
		justify-content: center;
		align-items: center;
		min-height: (177em / 25);
		padding: {
			left: 15%;
			right: 15%;
			top: 10px;
			bottom: 10px; }

		font-size: (25em / 18);
		line-height: (37 / 25);

		background-color: $blue;
		color: #ffffff;
		box-sizing: border-box;

		@include media(575px) {
			min-height: 6em;
			padding: {
				left: 5%;
				right: 5%; }
			font-size: (20em / 18); } }

	&__list {
		padding: 5px;

		background-color: #fff;

		li {
			display: flex;
			justify-content: center;
			align-items: center;
			min-height: (120em / 20);
			margin: {
				bottom: 5px; }
			padding: {
				left: 15%;
				right: 15%;
				top: 10px;
				bottom: 10px; }

			font-size: (20em / 18);
			font-weight: 300;
			line-height: (28 / 20);

			background-color: rgba($blue, 0.1);
			box-sizing: border-box;

			@include media(575px) {
				padding: {
					left: 5%;
					right: 5%; }
				font-size: (16em / 18); }

			&:last-child {
				margin-bottom: 0; } } } }
