.shipping {
	font-size: 16px;

	@include media(1600px) {
		font-size: 14.23px; }

	@include media(767px) {
		font-size: 16px; }

	&__title {
		font-size: (30em / 16);
		font-weight: 700;
		line-height: (50 / 30); }

	&__text {
		max-width: (830em / 18);
		margin: {
			top: (20em / 18);
			bottom: (50em / 18); }
		font-size: (18em / 16);
		font-weight: 300;
		line-height: (28 / 18); }

	&__subtitle {
		font-size: (18em / 16);
		font-weight: 700;
		line-height: (30 / 18); }

	&__list {
		margin-top: 0.5em;
		margin-bottom: (30em / 18);
		font-size: (18em / 16);
		font-weight: 300;
		line-height: 40px; }

	&__table {
		overflow: auto;

		table {
			width: 100%;
			border: 1px solid #fff;
			text-align: center;
			font-size: (18em / 16);
			line-height: (30 / 18);
			white-space: nowrap;

			th {
				font-weight: 700; }

			td, th {
				padding: (15em / 18) (30em / 18);
				border: 1px solid #fff; }


			thead {
				td {
					width: 21.67%;
					background-color: #eff7fb; }
				th {
					width: 26.11%;
					background-color: #e4f1f8; } }

			tbody {
				th {
					width: 21.67%;
					background-color: #eff7fb;
					text-align: left; }
				td {
					width: 26.11%;
					background-color: #e4f1f8; } } } } }


