.best {
	position: relative;
	padding: {
		top: 119px;
		bottom: 106px; }
	background-color: #f6fafc;

	@include media(1199px) {
		padding: {
			top: 45px;
			bottom: 90px; } }

	@include media(767px) {
		padding: {
			top: 25px;
			bottom: 50px; } }

	&__container {
		max-width: 1400px;
		margin: auto;

		box-sizing: border-box;

		@include media(1699px) {
			max-width: 100%;
			padding-left: calc(8vw + 30px);
			padding-right: 30px; }

		@include media(1199px) {
			padding-left: 30px; }

		@include media(767px) {
			padding: {
				left: 16px;
				right: 16px; } } }

	&__list {
		position: relative;
		margin-top: 68px;
		font-size: 18px;

		overflow: visible;

		&::before,
		&::after {
			content: '';

			position: absolute;
			top: -10px;
			bottom: -10px;
			z-index: 2;

			width: calc((100vw - 1400px) / 2);

			pointer-events: none;
			transition: opacity 0.3s; }

		&::before {
			right: 100%;
			background-image: linear-gradient(to right, #f6fafc, transparent); }

		&::after {
			left: 100%;
			background-image: linear-gradient(to left, #f6fafc, transparent); }

		&_reach-end {
			&::after {
				opacity: 0; } }

		.swiper-slide {
			width: 350px; }

		.swiper-pagination {
			display: none; }

		&_reach-end::before {
			opacity: 0; }

		@include media(1699px) {
			&::before,
			&::after {
				width: 15vw; } }

		@include media(1199px) {
			margin-top: 75px;
			padding-bottom: 90px;

			&::before,
			&::after {
				display: none; }

			.swiper-slide {
				width: 280px; }

			.swiper-pagination {
				display: block; } }

		@include media(767px) {
			margin-top: 30px;
			padding-bottom: 50px; } }

	&__control {
		position: absolute;
		top: 50%;
		z-index: 3;

		&_prev {
			right: 100%;
			transform: translateY(-50%) rotate(-180deg);

			@include media(1699px) {
				left: 0;
				right: auto; } }

		&_next {
			left: 100%;
			transform: translate(143px, -50%);

			@include media(1859px) {
				transform: translate(50px, -50%); }

			@include media(1699px) {
				left: auto;
				right: 0;
				transform: translate(0, -50%); } }

		&_disabled {
			opacity: 0;
			pointer-events: none; }

		@include media(1199px) {
			display: none; } }

	&__button-wrapper {
		margin-top: 92px;
		color: $darkblue;
		font-size: 22px;
		font-weight: bold;

		button {
			margin-left: 121px; }

		@include media(1199px) {
			display: none; }

		@include media(767px) {
			display: block;
			margin-top: 45px;

			text-align: center;

			button {
				display: block;
				margin: 19px auto 0; } } } }
