.cafe-history {
	position: relative;
	padding: {
		top: (115em / 18);
		bottom: (160em / 18); }
	font-size: 18px;

	background-color: $darkblue;
	color: #fff;

	@media screen and (min-width: 768px) and (max-width: 1600px) {
		font-size: 16px; }

	@media screen and (min-width: 1200px) and (max-width: 1459px) {
		padding-left: 138px; }

	@include media(767px) {
		padding: {
			top: 40px; } }

	&__title {
		color: inherit; }

	&__slider-row {
		position: relative;

		@include media(1199px) {
			padding: {
				right: (210em / 18); } }

		@include media(767px) {
			padding-right: 0; } }

	&__slider {
		margin-top: (100em / 18);

		.swiper-slide {
			height: auto; }

		@include media(575px) {
			margin-top: 40px; } }

	&__slide {
		display: flex;
		height: 100%;
		background-color: $darkblue;

		@include media(575px) {
			display: block; } }

	&__image {
		flex-basis: 320px;

		img {
			max-width: 100%; }

		@include media(1199px) {
			flex: 0 1 28%;
			max-width: 28%; }

		@include media(575px) {
			max-width: 100%;
			height: 110vw;
			text-align: center;

			img {
				width: 100%;
				height: 100%;
				object-fit: cover; } } }

	&__text {
		padding: {
			left: (160em / 18); }

		h3 {
			max-width: (540em / 26);

			font-weight: 600;
			font-size: (26em / 18);
			line-height: (38 / 26);
			margin-bottom: (43em / 26); }

		p {
			margin-bottom: 1em;
			max-width: (540em / 18);

			font-weight: 300;
			line-height: 1.44; }

		@media screen and (max-width: 1459px) {
			padding-left: 5vw; }

		@include media(575px) {
			padding-left: 0;
			padding-top: 2em;
			padding-bottom: 1em;
			text-align: center; } }

	&__pagination {
		position: absolute;
		top: -3em;
		right: 0;
		bottom: 0;
		left: auto;
		z-index: 1;

		padding-right: 65px;
		box-sizing: border-box;

		@include media(767px) {
			top: 100%;
			bottom: auto;

			width: 100%;
			margin-top: 2em;
			padding-right: 0; }


		.swiper-slide {
			position: relative;
			display: flex;
			align-items: center;

			font-size: (46em / 18);

			transform-origin: 100% 50%;
			opacity: 0.3;
			transform: scale(0.39);

			transition-property: transform, opacity;
			transition-duration: 0.3s;
			cursor: pointer;

			@include media(767px) {
				transform-origin: 50% 50%; }

			&::after {
				content: '•';
				position: absolute;
				left: 100%;
				top: 50%;
				margin-left: 35px;
				transform: translateY(-50%);
				opacity: 0;
				font-size: 0.7em;

				@include media(767px) {
					display: none; } }

			&-prev,
			&-next {
				transform: scale(0.54); }

			&-active {
				transform: scale(1);
				opacity: 1;

				&::after {
					opacity: 1; } } } }



	&__bullet {
		display: inline-block;
		padding: 0;
		appearance: none;
		background: none;
		border: none;
		font-size: 1em;
		color: inherit;

		letter-spacing: 2.88px;

		&:focus {
			outline: none; } } }
