.button {
	display: inline-block;
	min-width: 10em;
	padding: 1em 1.875em;

	border: 2px solid;

	font-family: Poppins;
	font-size: 1rem;
	font-weight: 400;
	letter-spacing: 1px;
	text-transform: uppercase;
	text-decoration: none;
	text-align: center;

	border-radius: 2.1875em;
	box-sizing: border-box;
	appearance: none;
	cursor: pointer;
	white-space: nowrap;

	transition-property: box-shadow, background-color, color;
	transition-duration: 0.3s;
	transition-timing-function: ease;

	&:focus {
		outline: none;
		box-shadow: 0 0 0 1px #fff, 0 0 2px 2px $blue; }

	&_blue {
		border-color: $blue;
		background-color: $blue;
		color: #fff;

		&:hover {
			background-color: lighten($blue, 10%); } }

	&_white {
		border-color: #fff;
		background-color: #fff;
		color: $blue;
		&:hover {
			background-color: lighten($blue, 30%); } }

	&_transparent {
		border-color: $blue;
		background-color: transparent;
		color: $blue;

		&:hover {
			background-color: rgba($blue, 0.3); } }

	&_transparent-white {
		border-color: #fff;
		background-color: transparent;
		color: #fff;
		&:hover {
			background-color: rgba(white, 0.2); } }


	&_round {
		position: relative;
		width: 40px;
		height: 40px;
		min-width: 1px;
		padding: 0;

		background-color: $blue;
		color: #fff;

		appearance: none;
		border: 2px solid $blue;
		border-radius: 50%;
		font-size: 24px;

		transition: box-shadow 0.3s;

		span {
			position: absolute;
			top: 50%;
			left: 50%;

			transform: translate(-50%, -50%);
			transition-property: opacity, transform;
			transition-duration: 0.3s;

			&:nth-child(2) {
				opacity: 0;
				transform: translate(-50%, -50%) rotate(45deg); } }


		&.active {
			background-color: transparent;
			color: $blue;

			span {
				&:nth-child(1) {
					opacity: 0;
					transform: translate(-50%, -50%) rotate(-45deg); }
				&:nth-child(2) {
					opacity: 1;
					transform: translate(-50%, -50%) rotate(0); } } }

		&:focus {
			outline: none;
			box-shadow: 0 0 0 2px #fff, 0 0 0 4px $blue; }

		&:active {
			box-shadow: 0 0 0 2px #fff, 0 0 0 2px $blue; } }

	&_icon {
		position: relative;

		padding: {
			left: 74px; }

		letter-spacing: 0.89px;

		svg {
			position: absolute;
			left: 35px;
			top: 50%;

			width: 21px;
			height: 20px;

			transform: translateY(-50%); } }

	&_vid {
		position: relative;
		padding: {
			left: 48px; }

		svg {
			position: absolute;
			left: 14px;
			top: 50%;

			width: 25px;
			height: 25px;

			transform: translateY(-50%); }

		@include media(767px) {
			width: 55px;
			height: 55px;
			min-width: 0;
			padding: 0;

			box-sizing: border-box;

			svg {
				left: 50%;

				transform: translate(-50%, -50%); }

			span {
				display: none; } } } }

