.slider-control {
	width: 74px;
	height: 74px;
	padding: 0;
	border: none;

	box-shadow: 0 0 12px 2px rgba(14, 61, 94, 0.13);
	background-color: #ffffff;
	border-radius: 50%;
	appearance: none;

	// opacity: 0
	// pointer-events: none

	color: $blue;

	transition-property: opacity, background-color, color;
	transition-duration: 0.3s;
	transition-timing-function: linear;
	cursor: pointer;
	-webkit-tap-highlight-color: transparent;

	svg {
		width: 100%;
		height: 100%;
		pointer-events: none; }

	&:hover {
		background-color: $blue;
		color: white; }

	&:focus {
		outline: none;
		box-shadow: 0 0 0 1px #fff, 0 0 5px 2px $blue; }

	&_prev {
		transform: rotate(-180deg); } }

.swiper-container,
.testimonials {

	.swiper-pagination {
		bottom: 0; }

	.swiper-pagination-bullet {
		width: 10px;
		height: 10px;
		margin: 0 5px !important;

		border: 1px solid $blue;
		box-sizing: border-box;

		background-color: transparent;
		opacity: 1;

		&:focus {
			outline: none;
			box-shadow: 0 0 0 1px #fff, 0 0 0 2px rgba($blue, 0.6); } }

	.swiper-pagination-bullet-active {
		background-color: $blue; } }
