.cart-settings {
	padding: {
		top: 30px;
		bottom: 150px; }

	@include media(767px) {
		padding: {
			bottom: 60px; } }

	@include media(575px) {
		padding: {
			top: 15px; } }

	color: $darkblue;

	&__steps {
		display: flex;
		justify-content: space-between;
		margin: {
			bottom: 15px; }

		.cart-step {
			flex-basis: calc(33.333% - 20px);

			@include media(575px) {
				flex-basis: calc(33.333% - 5px); } } }

	&__title {
		color: $blue;
		font-family: "Mont";
		font-size: 40px;
		font-weight: 800;
		line-height: 1.55;

		@include media(575px) {
			font-size: 20px;
			line-height: 1.5; } }

	&__box {
		margin: {
			top: 45px;
			bottom: 35px; }
		padding: 35px 60px 60px;
		background-color: #ffffff;
		color: $darkblue;

		&_softblue {
			background-color: rgba($blue, 0.12);

			.input-text__input {
				border-bottom-color: rgba(100, 178, 219, 0.54); } }

		&_flex {
			display: flex;

			@include media(767px) {
				flex-wrap: wrap; } }

		&_personal {
			margin-top: 0;

			@media screen and (min-width: 1200px) {
				padding: 60px 100px; } }

		&_p-100 {
			@media screen and (min-width: 1200px) {
				padding: 60px 100px; } }

		&_password {
			max-width: 650px;
			margin-top: 0;
			padding: 4.06em 4.375em;
			box-sizing: border-box;

			.button {
				margin-top: 2.5em; }

			@include media(991px) {
				margin: {
					left: auto;
					right: auto; } } }

		&_payment-opts {
			padding: {
				top: 40px;
				bottom: 40px; }

			.input-radio {
				margin-right: 100px;

				&:last-child {
					margin-right: 0; } } }


		@include media(575px) {
			margin: {
				top: 18px;
				bottom: 15px; }
			padding: 10px 20px 40px; } }

	&__user-check {
		flex-basis: 48%;

		h3,
		h4 {
			padding-right: 20px;
			font-family: "Mont";
			font-size: 18px;
			line-height: 1.444;
			text-transform: uppercase; }
		h4 {
			margin-bottom: 55px;
			font-weight: 800; }
		h3 {
			font-weight: 200; }

		&>div {
			margin-bottom: 20px; }

		@include media(1199px) {
			flex-basis: 50%; }

		@include media(767px) {
			flex-basis: 100%; }

		@include media(575px) {
			h4 {
				margin-bottom: 35px;
				padding-right: 0; }
			h3 {
				padding-right: 0; } } }

	&__sign-in {
		flex-basis: 45%;

		h3,
		h4 {
			font-family: "Mont";
			font-size: 18px;
			line-height: 1.444;
			text-transform: uppercase; }
		h4 {
			margin-bottom: 38px;
			font-weight: 800; }
		h3 {
			font-weight: 200; }

		.input-text {
			margin-bottom: 17px; }

		@include media(1199px) {
			flex-basis: 50%; }

		@include media(767px) {
			flex-basis: 100%;
			padding-top: 5px; } }

	&__pass-recovery {
		margin-top: 10px;
		padding: 0;
		border: none;
		background: none;
		appearance: none;
		opacity: 0.6;
		font-size: 14px;
		font-weight: 400;

		@include media(575px) {
			margin-top: 8px; } }

	&__sign-submit {

		.button {
			min-width: 164px;
			margin-top: 32px;
			margin-bottom: 15px; }

		@include media(767px) {
			text-align: center; } }

	&__forms {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		color: $darkblue; }

	&__form-item {
		flex-basis: calc(50% - 20px);
		margin-bottom: 20px;

		&_single {
			flex-basis: 100%; }

		@include media(767px) {
			flex-basis: 100%; } }

	&__billing-radios {
		margin-top: 25px;
		div {
			margin-bottom: 20px; } }

	&__label {
		opacity: 0.3;
		color: #0e3d5e;
		font-size: 0.875em;
		line-height: 1.5;
		text-transform: none; }

	&__shipping-radios {
		.input-radio {
			margin-top: 14px;
			margin-right: 20px;

			&:last-of-type {
				margin-right: 0; } } }

	&__confirm {
		max-width: 80%;
		margin-top: 40px;
		line-height: 1.5;

		@include media(575px) {
			max-width: 100%; } }

	&__send {
		width: 100%;
		margin-top: 60px;
		text-align: center; }

	&__payment,
	&__confirmation {
		padding: {
			top: 35px; }

		@include media(575px) {
			padding: {
				top: 15px; } } }

	&__personal-btn {
		margin-top: 5.9em;
		text-align: right;

		@include media(575px) {
			.button {
				width: 100%; } } }

	&__create-acc {
		margin: {
			top: (30em / 16); } } }

.cart-step {
	padding: {
		top: 5px;
		bottom: 5px; }
	color: $blue;
	letter-spacing: 0.8px;
	line-height: 1.688;

	border-bottom: 5px solid $blue;
	opacity: 0.1;

	&_active {
		opacity: 1; }

	@include media(767px) {
		text-align: center;
		&__text {
			display: none; }
		&__num {
			font-weight: 600;
			span {
				display: none; } } } }
