.offer-tile {
	position: relative;

	text-align: left;
	color: #fff;
	background-size: cover;
	background-repeat: no-repeat;

	&::before {
		content: '';

		position: absolute;
		top: 43.57%;
		left: 0;
		right: 0;
		bottom: 0;
		z-index: 0;

		background-image: linear-gradient(180deg, rgba(14, 61, 94, 0) 0%, #0e3d5e 100%); }

	&__wrapper {
		position: relative;
		z-index: 1;

		display: flex;
		flex-direction: column;
		justify-content: flex-end;
		align-items: flex-start;
		min-height: (700em / 18);
		padding: {
			left: 30px;
			right: 30px;
			bottom: 20px; }

		box-sizing: border-box;


		@include media(1199px) {
			min-height: (400em / 18); } }

	&__offer-num {
		margin-bottom: 10px;
		font-family: "Mont";
		font-size: (16em / 18);
		font-weight: 800;
		letter-spacing: (3em / 16);
		line-height: 1;
		text-transform: uppercase;

		@include media(1199px) {
			font-size: (14em / 18); } }

	&__title {
		min-height: 2em;
		font-family: "Mont";
		font-size: (40em / 18);
		font-weight: 800;
		line-height: (52 / 40);

		@include media(1199px) {
			font-size: (20em / 18); } }

	&__button {
		margin: {
			bottom: 10px; }

		@include media(1199px) {
			font-size: (14em / 18); } }

	&__cups {
		margin: {
			bottom: 30px; }

		color: #ffffff;
		font-size: (22em / 18);
		line-height: (37 / 22);

		@include media(1199px) {
			font-size: (14em / 18); } }

	&__icon {
		width: 40px;
		height: 34px;
		margin-right: 23px;

		color: $blue; }

	&__pricing {
		margin-top: (8em / 16);
		font-size: (16em / 18);
		font-weight: 300;
		line-height: (26 / 16);

		strong {
			font-weight: 600; } } }
