.coffee-types {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	font-size: 30px;
	line-height: 1.4;

	@include media(1600px) {
		font-size: 24px; }

	@include media(767px) {
		font-size: 20px; }

	&__list {

		padding-top: 2.33em;

		li {
			margin-bottom: 2.33em; }

		a {
			display: flex;
			align-items: center;
			color: inherit;
			text-decoration: none;

			&:hover {
				color: $blue; } } }

	&__icon {

		&-wrapper {
			flex: 0 0 5em;
			width: 5em;
			text-align: left; }

		&_beans {
			width: (63em / 30);
			height: (63em / 30); }
		&_cezve {
			width: (75em / 30);
			height: (47em / 30); }
		&_cup {
			width: (60em / 30);
			height: (60em / 30); }
		&_capsule {
			width: (77em / 30);
			height: (46em / 30); } } }
