.cart-link {
	position: relative;

	padding: 0;
	border: none;

	appearance: none;
	background: none;
	color: inherit;
	transition: color 0.3s;

	cursor: pointer;

	&__icon {
		width: 19px;
		height: 22px;
		color: $blue; }

	&__counter {
		position: absolute;
		right: 0;
		bottom: 7px;

		display: inline-flex;
		justify-content: center;
		align-items: center;
		width: 18px;
		height: 18px;

		background-color: $blue;
		color: #fff;
		line-height: 18px;
		font-size: 11px;

		transform: translateX(50%);
		transition: background-color 0.3s;
		border-radius: 50%; }


	&:hover {
		opacity: 0.9;
		color: $blue; }

	&:hover & {
		&__counter {
			background-color: $blue; } } }

