.faq-page {
	&__content {
		padding: {
			top: 27px; } }
	&__title {
		margin-bottom: 26px; }

	&__item {
		margin-top: 20px;
		padding: 1.75em 2em;

		font-size: 20px;
		background-color: #ffffff;

		@include media(767px) {
			font-size: 16px; }

		@include media(575px) {
			padding: 15px; } }

	&__question {
		display: flex;
		justify-content: space-between;
		align-items: center;

		label {
			flex-grow: 1;
			padding-right: 1em; }

		&.active {
			padding-bottom: 30px;
			border-bottom: 1px solid rgba(#dfe7eb, 0.5);

			label {
				font-weight: 600;
				color: $blue; }

			@include media(575px) {
				padding-bottom: 15px; } } }

	&__show-answer {
		flex-shrink: 0; }

	&__answer {
		display: none;
		padding: {
			top: (30em / 18);
			bottom: (30em / 18); }
		font-size: (18em / 20);
		font-weight: 300;
		line-height: 1.5;

		&.visible {
			display: block; }

		@include media(575px) {
			padding: {
				top: 15px;
				bottom: 15px; } } } }
