.header-contacts {
	display: flex;
	// flex-wrap: wrap
	margin-top: (60em / 18);
	margin-bottom: auto;

	font-size: 18px;

	@include media(1919px) {
		font-size: calc(0.5vw + 7px); }

	@include media(767px) {
		flex-wrap: wrap;
		font-size: 16px;
		margin-bottom: 16%;
		text-align: center; }

	&__col {
		flex: 0 0 50%;
		padding-right: 10%;

		box-sizing: border-box;

		@include media(767px) {
			flex-basis: 100%;
			padding-right: 0; } }

	&__block {
		margin-bottom: 4vh; }

	&__heading {
		margin-bottom: 0.75em;
		font-size: (16em / 18);
		font-weight: 300;
		letter-spacing: 1px;
		line-height: (21 / 16);
		text-transform: uppercase; }

	&__content {
		font-weight: 500;
		line-height: (26 / 18);

		a {
			display: block;
			color: inherit;
			transition: color 0.2s;

			&:hover {
				color: $blue; } } }

	&__button {
		margin-top: 2vh; }

	i {
		font-style: italic;
		text-transform: none; } }
