.filters {
	width: 100%;
	padding: {
		left: 2.28em;
		top: 2em;
		right: 1.67em;
		bottom: 2em; }

	line-height: (40em / 18);

	box-sizing: border-box;

	&__category {
		margin-bottom: (30em / 16); }

	&__title {
		display: flex;
		justify-content: space-between;
		align-items: center;

		font-size: 1.333em;
		font-weight: 600;
		color: $darkblue;

		svg {
			width: 12px;
			height: 7px;
			transition: transform 0.3s; }

		&:focus {
			outline: none; }

		&_collapsed {

			svg {
				transform: rotate(-90deg); } } }

	&__list {
		li {
			position: relative; }



		&_hidden {
			display: none; } }

	&__checkbox {

		input {
			&:checked + label {

				font-weight: 600;
				color: $blue; } } }

	&__view-more {
		display: none;

		@include media(991px) {
			display: block;
			margin-top: (38em / 16); } } }
