.page-404 {
	margin-top: 10vh;
	font-family: "Mont";
	&__code {
		display: flex;
		align-items: flex-end;
		font-size: 200px;
		font-weight: 800;
		line-height: 1;
		color: $blue;

		filter: drop-shadow(0 0 80px rgba(white, 0.3));

		img {
			max-width: 100%; }

		@include media(767px) {
			justify-content: center;
			font-size: 160px; } }

	&__text {
		font-size: 27px;
		font-weight: 800;
		transform: translateY(-100%);
		white-space: nowrap;

		@include media(767px) {
			text-align: center;
			font-size: 22px; } } }
