.coffee-club {
	@extend %default-block;

	&__desc {
		max-width: 80%;
		margin: (40em / 20) auto 0;

		font-size: (20em / 18);
		font-weight: 300;
		line-height: 1.8;

		text-align: center;

		@include media(767px) {
			max-width: 100%; } }

	&__row {
		display: flex;
		flex-wrap: wrap;
		margin-top: (80em / 18); }

	&__col {
		display: flex;
		flex-direction: column;
		flex-basis: 50%;

		padding: {
			right: 10%; }

		box-sizing: border-box;

		@include media(1199px) {
			padding-right: 5%; }

		@include media(767px) {
			flex-basis: 100%;
			margin-bottom: 4em;
			padding-right: 0;

			&:last-child {
				margin-bottom: 0; } } }

	&__icon {
		height: (110em / 18);

		svg {
			max-width: 100%; }

		&_offer {
			svg {
				width: 80px;
				height: 79px; } }
		&_membership {
			svg {
				width: 104px;
				height: 62px; } } }

	&__heading {
		margin-bottom: auto;
		font-size: (20em / 18);
		font-weight: 700;
		line-height: 1.5; }

	&__list {
		margin-bottom: 0;
		margin-top: 23px; } }
