.testimonials {
	position: relative;

	display: flex;
	padding: {
		top: 153px;
		bottom: 170px; }

	background-color: #fff;

	@include media(1699px) {
		padding-left: calc(8vw + 30px);
		padding: {
			top: 90px;
			bottom: 110px; } }

	@include media(1199px) {
		padding: {
			top: 70px;
			left: 0; } }

	@include media(767px) {
		flex-wrap: wrap;
		padding-top: 34px;
		padding-bottom: 50px; }

	&__col {
		flex-grow: 0;

		box-sizing: border-box;

		&:nth-child(1) {
			flex-basis: calc(50% + 30px);
			padding-right: 184px; }

		&:nth-child(2) {
			position: relative;
			flex-basis: calc(50% - 30px);
			max-width: calc(50% - 30px);
			padding-top: 10px; }


		@include media(1199px) {

			&:nth-child(1) {
				flex: 0 0 41%;
				min-width: 41%;
				padding: {
					left: 8.79%;
					right: 7.52%; } }

			&:nth-child(2) {
				flex: 0 0 59%;
				max-width: 59%; } }


		@include media(767px) {
			&:nth-child(1) {
				width: 100%;
				max-width: 100%;
				flex-basis: 100%;
				padding-right: 0;
				background: none;

				text-align: center; }

			&:nth-child(2) {
				flex-basis: 100%;
				max-width: 100%;
				padding-left: 16px;
				padding-top: 56px; } } }


	&__title {
		max-width: 420px;
		margin-right: 0;
		margin-left: auto;
		text-align: right;

		@include media(1199px) {
			max-width: 270px; }

		@include media(767px) {
			margin: auto;
			text-align: center; } }

	&__list {
		position: relative;

		padding-bottom: 80px;

		.swiper-wrapper {
			padding-right: calc(50vw - 30px); }

		.swiper-slide {
			width: 375px;
			padding-left: 15px;
			box-sizing: border-box; }

		&::after {
			content: '';

			position: absolute;
			right: 0;
			top: 0;
			bottom: 10px;
			z-index: 2;

			width: 350px;
			opacity: 1;

			background-image: linear-gradient(to right, rgba(255, 255, 255, 0) 0%, #ffffff 100%);

			transition: opacity 0.3s; }

		&_reach-end::after {
			opacity: 0; }

		@include media(1199px) {
			margin-left: 0;

			&::before,
			&::after {
				display: none; }

			.swiper-slide {
				width: 280px;
				padding-left: 0; } }

		@include media(767px) {
			padding: {
				bottom: 40px; } } }

	&__control {
		position: absolute;
		top: 229px;
		z-index: 3;

		&_prev {
			right: 100%;
			margin-right: 30px; }

		&_next {
			right: 22px; }

		@include media(1199px) {
			display: none; } }

	&__scroll {
		position: absolute;
		left: 0;
		bottom: 0;

		width: calc(100% - 92px);
		height: 7px;
		margin-left: 15px;

		border-radius: 2px;
		background-color: #f6fafc;

		.swiper-scrollbar-drag {
			border-radius: 2px;
			background-color: $blue; }

		@include media(1199px) {
			display: none; } }

	&__pagination {
		display: none;

		@include media(1199px) {
			position: absolute;
			bottom: 118px;
			left: 50%;
			z-index: 1;

			display: block;

			transform: translateX(-50%);

			text-align: center; }

		@include media(767px) {
			position: static;

			width: 100%;

			transform: none; } } }
