.pay-systems {
	display: flex;
	padding-top: 10px;

	li {
		margin-right: 7px; }

	&__input {
		position: absolute;
		width: 1px;
		height: 1px;
		padding: 0;
		border: none;

		overflow: hidden;
		background: none;
		appearance: none;

		&:focus {
			outline: none;

			+ .pay-systems__icon {
				border-color: #d8d8d8; } }

		&:checked {
			+ .pay-systems__icon {
				border-color: $blue; } } }

	&__icon {
		display: inline-block;
		width: 58px;
		height: 36px;
		border: 1px solid transparent;

		border-radius: 2px;
		box-shadow: 0 0 3px rgba(25, 70, 94, 0.12);
		background-color: #d8d8d8;

		background-repeat: no-repeat;
		background-size: 188px 36px;

		box-sizing: border-box;

		&_visa {
			background-position: -1px -1px; }

		&_mastercard {
			background-position: -66px -1px; }

		&_paypal {
			background-position: -131px -1px; } } }

.pay-systems__icon {
	background-image: url(../img/common/pay-systems-sprite.png); }

.webp .pay-systems__icon {
	background-image: url(../img/common/pay-systems-sprite.webp); }
