.input-checkbox {
	position: relative;
	&__real {
		position: absolute;
		top: 1.1em;
		left: 4px;

		width: 1px;
		height: 1px;
		padding: 0;

		appearance: none;
		overflow: hidden;

		&:checked + label {

			&::before {
				background-color: #64b2db;
				background-image: url("data:image/svg+xml,%3Csvg width='11' height='9' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M.127 4.95A.502.502 0 0 1 0 4.635c0-.09.042-.225.127-.315l.592-.63c.17-.18.423-.18.593 0l.042.045L3.68 6.39c.084.09.211.09.296 0L9.647.135h.041c.17-.18.424-.18.593 0l.592.63c.17.18.17.45 0 .63l-6.77 7.47A.374.374 0 0 1 3.809 9a.374.374 0 0 1-.296-.135l-3.3-3.78z' fill='%23fff'/%3E%3C/svg%3E");
				background-position: center;
				background-repeat: no-repeat; } }

		&:focus {
			outline: none;

			& + label::before {
				box-shadow: 0 0 0 2px white, 0 0 0 4px rgba($blue, 0.5); } } }

	&__label {
		position: relative;

		font-weight: 300;
		color: rgba($darkblue, 0.5);

		user-select: none;
		cursor: pointer;

		&::before {
			content: '';

			display: inline-block;
			width: 15px;
			height: 15px;
			margin-right: 14px;

			background-color: #e5eaed;

			transition: box-shadow 0.3s; } } }

