.subscription-settings {
	font-size: 16px;

	@media screen and (min-width: 1200px) {

		padding: 3.375em 6.25em 7.81em; }

	@include media(575px) {
		font-size: 14px; }

	&__title {
		font-size: 1.875em;
		line-height: 1.66;

		@include media(575px) {
			line-height: 1.4; } }

	&__desc {
		max-width: 30.83em;
		margin: {
			top: 15px; }
		font-size: 1.125em;
		line-height: 1.5;
		font-weight: 300; }

	&__form {
		max-width: 20em;
		padding-top: 3.44em;
		font-size: 1.125em; }

	&__radio-group {
		margin: {
			top: 3.11em; } }

	&__subscribe {
		margin-top: 4.125em; } }
