.confirmation {

	@include media(1199px) {
		font-size: 0.85em; }

	@include media(767px) {
		font-size: 0.8em; }

	&__box {
		padding: {
			top: 0.9375em;
			right: 4.375em;
			bottom: .25em;
			left: 4.375em; }
		background-color: #ffffff;

		@include media(575px) {
			padding: {
				left: 15px;
				right: 15px; } } }

	&__title {
		padding: {
			top: 30px;
			bottom: 30px; }

		border-bottom: 1px solid #dfe7eb;

		font-size: 1.875em;
		font-weight: 800;
		line-height: 1.5;
		text-transform: uppercase; }

	&__row {
		display: flex;
		align-items: center;
		justify-content: space-between;
		padding: {
			top: 1.875em;
			bottom: 1.875em; }

		border-bottom: 1px solid #dfe7eb;
		font-size: 1.25em;
		font-weight: 300;

		span {
			&:nth-child(odd) {
				padding-right: 2em;

				@include media(575px) {
					padding-right: 0; } }

			&:nth-child(even) {
				padding-left: 2em;
				font-size: 0.9em;
				text-align: right;

				@include media(575px) {
					text-align: left;
					padding-left: 0; } } }

		&_history {
			border-bottom: none;

			h3 {
				padding-right: 2em;
				font-weight: 700; } }

		@include media(575px) {
			display: block;

			span {
				display: block;
				margin-bottom: 1em; }

			h3 {
				margin-bottom: 1em; } } }

	&__link {
		font-weight: 600;
		color: $blue;
		white-space: nowrap; } }



