.manage-account {
	width: 100%;
	padding: {
		top: 50px; }

	font-size: 16px;

	box-sizing: border-box;

	@include media(575px) {
		font-size: 13px; }

	&__title {
		padding: {
			left: 2.22em; }
		color: $darkblue;
		font-family: Poppins;
		font-size: 1.125em;
		font-weight: 600;
		text-transform: uppercase; }

	&__menu {
		margin: {
			top: 20px; }
		color: #b8c3c7;
		font-size: 1.25em;
		font-weight: 300; }

	&__item {

		border-left: 5px solid transparent;

		a {
			display: inline-block;
			width: 100%;
			padding: {
				left: 1.75em;
				top: 0.75em;
				bottom: 0.75em;
				right: 1.75em; }

			color: inherit;
			text-decoration: none;
			line-height: 1.5;
			box-sizing: border-box;

			&:hover {
				color: rgba($blue, 0.8); } }

		&_current {
			border-left-color: $blue;
			color: $blue;
			font-weight: 500; } } }
