.banner {
	display: flex;
	flex-direction: column;
	font-size: 18px;

	@include media(1600px) {
		font-size: 16px; }

	@include media(767px) {
		text-align: center; }

	&__title {
		margin-top: 2.2em;

		font-size: 2.78em;

		@include media(767px) {
			margin-top: 1.8em; } }

	&__icon {

		margin-top: 3em;

		&_coffee {
			width: 2.11em;
			height: (45em / 18);
			margin-right: 30px;
			fill: blue; }

		@include media(767px) {
			margin: {
				top: 3em;
				left: auto;
				right: auto; } } }

	&__text {
		max-width: 16.36em;
		margin: {
			top: 3.63em;
			bottom: 5em; }
		font-size: 1.222em;
		font-weight: 400;
		line-height: 2.09;

		@include media(767px) {
			margin: {
				top: 2em;
				bottom: 3em;
				left: auto;
				right: auto; } } }

	&__btn-group {
		display: flex;

		@include media(767px) {
			justify-content: center; }

		@include media(575px) {
			display: block; } }

	&__button {
		margin-right: 20px;

		font-size: 0.889em;

		@include media(1199px) {
			margin-right: 10px; }

		@include media(575px) {
			display: block;
			width: 215px;
			margin: 0 auto 10px; }

		&:last-child {
			margin-right: 0;

			@include media(575px) {
				margin-right: auto;
				margin-bottom: 0; } } }

	&__brands {
		display: flex;
		align-items: center;

		font-family: "Mont";
		font-weight: 300;
		line-height: 1.4;
		font-size: 1.1111em;

		span {
			padding-right: 1em;
			white-space: nowrap;

			@include media(575px) {
				display: none; } }

		ul {
			flex: 1 1 auto;
			display: flex;
			justify-content: space-around;
			align-items: center; }

		li {
			padding: 0 10px; }

		img {
			max-width: 100%; } }

	&__list {
		font-size: 1.11em;
		font-weight: 300;
		line-height: 1.6; }

	&_index & {

		&__title {
			margin-top: 3em;

			@include media(1199px) {
				margin-top: 2em; } }

		&__text {
			display: flex;
			max-width: 100%;
			margin-top: 2.5em;
			margin-bottom: 3.9em;

			font-family: "Mont";
			font-weight: 300;
			line-height: 1.4;
			font-size: 1.1111em;

			@include media(1199px) {
				margin: {
					top: 2em;
					bottom: 2em; } }

			@include media(767px) {
				font-size: 14px;
				text-align: center;
				justify-content: center; } }

		&__icon {
			margin-top: 0;
			color: $blue;
			@include media(767px) {
				display: none; } }

		&__btn-group {

			@include media(767px) {
				order: 1;
				display: block;
				width: 100vw;
				margin: {
					top: 50px;
					left: -15px; }
				padding: {
					top: 30px;
					bottom: 30px; }

				background-size: cover;
				background-position: top center;
				background-repeat: no-repeat; } }

		&__button {
			@include media(767px) {
				display: block;
				width: 215px;
				margin: 0 auto 10px;

				&:last-child {
					margin-bottom: 0; } } }

		&__brands {
			margin-top: 10em;

			@include media(1199px) {
				margin: {
					top: 5em; } }

			@include media(767px) {
				margin: {
					top: 37px; } } } }

	&_about-bbg & {

		&__icon {
			@media screen and (min-width: 768px) {
				margin-top: 6.67em; } } }

	&_our-cafe & {

		&__icon {
			@media screen and (min-width: 768px) {
				margin-top: 5.28em; } }

		&__text {
			@media screen and (min-width: 768px) {
				margin-top: 3.36em; } } }

	&_bbc & {

		&__icon {
			@media screen and (min-width: 768px) {
				margin-top: 7.22em; } } }

	&_gift & {

		&__text {
			max-width: 13.8em;
			margin-top: 1.8em;
			margin-bottom: 1.8em;
			font-size: 2em;
			line-height: 1.38;

			@include media(767px) {
				margin: {
					top: 1em;
					bottom: 1em; } } }

		&__list {
			margin: {
				top: 5.22em; }
			text-align: left;

			@include media(767px) {
				margin: {
					top: 2em;
					left: auto;
					right: auto; } } }

		&__button {
			@include media(767px) {
				flex-basis: 40%; } } }


	&_beans & {

		&__text {
			@media screen and (min-width: 1199px) {
				margin: {
					top: 3.5em;
					bottom: 1.8em; } } }

		&__list {
			margin-top: 0; } }

	&_businesses & {

		&__text {
			max-width: 18em;
			margin-top: 1.2em;
			margin-bottom: 2.63em;
			font-size: 1.66em;
			line-height: 1.38;

			@include media(767px) {
				margin: {
					top: 1em;
					bottom: 1em; }

				font-size: 1.3em; } }

		&__icon {
			margin: {
				top: 5.9em; }

			@include media(767px) {
				margin-top: 3em; } } }

	&_loyality & {

		&__text {
			@media screen and (min-width: 768px) {
				max-width: 20.45em;
				margin-top: 3.31em; } }

		&__icon {
			@media screen and (min-width: 768px) {
				margin-top: 8.89em; } } }

	&_flavored & {

		&__text {
			max-width: 20.83em;
			margin-top: 2.5em;

			font-size: 1.66em;
			line-height: 1.66;

			@include media(767px) {
				margin: {
					top: 1em;
					bottom: 1em; }

				font-size: 1.3em; } } }

	&_capsules & {

		&__icon {
			margin: {
				top: 10.3em;
				bottom: 3.9em; }

			@include media(1199px) {
				margin-top: 6em; } }

		&__list {
			display: flex;

			@include media(767px) {
				justify-content: center; }

			li {
				margin-right: 28px;
				&:first-child {
					padding-left: 0;

					&::before {
						display: none; } }

				&:last-child {
					margin-right: 0; } } } } }

@include media(767px) {
	.banner_index .banner__btn-group {
		background-image: url(../img/front-page/banner-xs.jpg); }

	.webp .banner_index .banner__btn-group {
		background-image: url(../img/front-page/banner-xs.webp); } }
