.offer-popup {
	position: absolute;
	top: (70em / 18);
	left: 0;
	z-index: 5;

	padding: {
		top: (100em / 18);
		left: (65em / 18);
		right: (65em / 18);
		bottom: (50em / 18); }

	text-align: left;

	box-shadow: 0 2px 11px 8px rgba(79, 156, 194, 0.05);
	background-color: #ffffff;
	transform: translateY(15px);
	opacity: 0;
	pointer-events: none;
	transition-property: opacity, transform;
	transition-duration: 0.1s;

	&.visible {
		transform: translateY(0);
		opacity: 1;
		pointer-events: auto;
		transition-duration: 0.3s; }

	@include media(1359px) {
		left: 10px;
		right: 10px; }

	@include media(1199px) {
		padding: {
			top: 80px;
			left: 30px;
			right: 30px;
			bottom: 40px; } }

	@include media(575px) {
		top: 40px;

		padding: {
			left: 10px;
			right: 10px; } }

	&__close {
		position: absolute;
		right: 24px;
		top: 31px;

		width: 27px;
		height: 27px;
		padding: 0;
		border: none;

		background: none;
		appearance: none;
		color: $blue;
		cursor: pointer;
		-webkit-tap-highlight-color: transparent;

		transition: color 0.2s;

		&::before,
		&::after {
			content: '';
			position: absolute;
			top: 50%;
			left: 50%;

			width: 34px;
			height: 2px;

			background: currentColor; }

		&::before {
			transform: translate(-50%, -50%) rotate(-45deg); }
		&::after {
			transform: translate(-50%, -50%) rotate(45deg); }

		&:hover {
			color: $darkblue; } }

	&__row {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between; }

	&__image {
		flex-basis: 380px;

		img {
			max-width: 100%; }

		@include media(1199px) {
			flex-basis: 255px; }

		@include media(575px) {
			flex-basis: 100%;
			text-align: center; } }

	&__header {
		flex-basis: calc(100% - 380px);
		padding-left: 13.6%;

		box-sizing: border-box;

		@include media(1199px) {
			flex-basis: calc(100% - 255px); }

		@include media(767px) {
			padding-left: 5%; }

		@include media(575px) {
			flex-basis: 100%;
			padding-top: 2em;
			text-align: center; } }

	&__heading {
		font-family: "Mont";
		font-size: (20em / 18);
		font-weight: 800;
		letter-spacing: (3.75em / 20);
		text-transform: uppercase; }

	&__subheading {
		color: $blue;

		@include media(767px) {
			text-align: left; }

		@include media(575px) {
			text-align: center; } }

	&__description {
		margin: {
			top: (60em / 20); }
		padding: {
			right: 17%; }

		font-size: (20em / 18);
		line-height: (32 / 20);
		font-weight: 600;

		p {
			margin-bottom: 1.5em; }

		@include media(991px) {
			padding-right: 0; }

		@include media(767px) {
			margin-top: 20px;
			font-size: (16em / 18); } }

	&__info {
		flex-basis: calc(33.3333% - 37.3333px);
		padding: {
			top: (70em / 18); }

		box-sizing: border-box;

		font-weight: 300;
		line-height: (26 / 18);

		@include media(767px) {
			flex-basis: calc(33.3333% - 10px);
			padding-top: 20px;
			font-size: 14px; }

		@include media(575px) {
			flex-basis: 100%; }

		h4 {
			padding: {
				bottom: (10em / 20); }
			border-bottom: 7px solid $blue;

			font-size: (20em / 18);
			font-weight: 700;

			svg {
				margin: {
					right: (25em / 20); }

				@include media(767px) {
					display: none; } } }

		p, ul {
			margin: 1em 0; }

		li {
			position: relative;

			margin: {
				bottom: 1em; }
			padding: {
				left: 20px; }

			&:before {
				content: '•';
				position: absolute;
				left: 0;
				top: 0;

				color: $blue; } } }

	&__signup {
		margin: {
			top: (70em / 18); }
		text-align: center;

		@include media(767px) {
			margin-top: 40px; } } }
