.our-offers {
	@extend %default-block;

	background-color: #f6fafc;

	&__list {
		display: flex;
		margin-top: 3.6em;

		@include media(767px) {
			margin-top: 40px; }

		li {
			flex-basis: 50%;
			margin-right: 30px;

			&:nth-child(even) {
				margin-right: 0; }

			@include media(767px) {
				margin-right: 10px; } } }

	&__card {
		display: block;
		width: 100%;
		height: 100%;
		padding: 40px;

		box-shadow: 0 2px 23px 3px rgba(49, 90, 112, 0.06);
		background-color: #ffffff;
		color: inherit;
		text-decoration: none;
		text-align: center;

		box-sizing: border-box;

		transition: box-shadow 0.3s;

		&:hover {
			box-shadow: 0 12px 26px 7px rgba(71, 121, 147, 0.2); }

		@include media(767px) {
			padding: 30px; }

		@include media(575px) {
			padding: 10px 10px 20px; } }

	&__image {
		display: flex;
		justify-content: center;
		align-items: center;
		height: 19.72em;

		@include media(767px) {
			height: 38vw; }

		div, picture {
			display: flex;
			justify-content: center;
			align-items: center;
			height: 100%; }

		img {
			max-width: 100%;
			max-height: 100%;
			min-height: 1px; } }

	&__title {
		line-height: 1.22;
		font-weight: 700;

		@include media(575px) {
			font-size: 14px; } }

	&__price {
		margin-top: 1em;
		font-size: 19px;
		font-weight: 700;
		color: $blue;

		@include media(575px) {
			font-size: 15px; } } }
