.page {
	color: $darkblue;

	&_has-filter &__container {
		margin-left: 343px; }

	@media screen and (min-width: 1920px) {

		&_has-filter &__container {
			margin: auto; } }

	@media screen and (min-width: 1200px) and (max-width: 1600px) {
		font-size: 14px; }

	@include media(991px) {
		&_has-filter &__container {
			margin: auto; } }

	&__container_catalog {
		@include media(1199px) {
			max-width: 580px; }

		@include media(767px) {
			max-width: 100%; } }

	&__header {
		display: flex;
		justify-content: space-between;
		align-items: center; }

	&__content {
		padding: {
			top: (50em / 16); }

		background-color: #f6fafc;

		@include media(1199px) {
			padding-top: 30px; }

		&_bluebox {
			@include media(991px) {
				background-color: #fff; } } }

	&__breadcrumb {
		@include media(1199px) {
			display: none; } }

	&__back-button {
		display: none;
		width: 30px;
		height: 22px;
		color: $darkblue;

		svg {
			width: 100%;
			height: 100%;
			transform: rotate(-90deg); }

		@include media(1199px) {
			display: inline-block; } }

	&__title {
		margin: {
			top: 10px; }
		color: $blue;
		font-family: "Mont";
		font-size: 40px;
		font-weight: 800;
		line-height: 1.24;

		@include media(1199px) {
			display: inline-block;
			margin-top: 0;
			margin-left: 20px;
			margin-right: auto; }

		@include media(991px) {
			margin-left: 15px;
			font-size: 30px; } } }
