.billing {
	padding-top: 0;
	padding-bottom: 150px;

	font-size: 16px;

	@include media(767px) {
		padding: {
			top: 20px;
			bottom: 40px; }

		font-size: 14px; }

	&__box {
		display: flex;
		justify-content: space-between;
		flex-wrap: wrap;
		margin: {
			top: 2.8125em;
			bottom: 2.1875em; }

		padding: 4em 5.625em 5.625em;

		background-color: #ffffff;

		.billing__col {
			margin-top: 1.625em; }

		@include media(767px) {
			margin: {
				top: 20px;
				bottom: 20px; }
			padding: 2em 3.625em 3.625em; }

		@include media(575px) {
			padding: 0 15px 20px;

			.billing__col {
				margin-top: 20px; } } }

	&__col {
		flex-basis: calc(50% - 15px);
		box-sizing: border-box;

		&_forms {
			padding-right: 15px; }

		@include media(1199px) {
			flex-basis: 100%;
			padding-right: 0; } }

	&__input {
		margin-bottom: 2.5em;

		&:last-child {
			margin-bottom: 0; } }

	&__table {
		margin-top: 2.8125em; } }

.subsciption-card {
	display: flex;
	height: 100%;
	font-size: 16px;

	@include media(575px) {
		flex-wrap: wrap; }

	&__col {
		padding: {
			top: 2.1875em;
			bottom: 2.1875em; }
		flex-basis: auto;

		&:first-child {
			padding: {
				left: 1.875em;
				right: 1.5625em; }
			background-color: $darkblue;
			color: #fff; }

		&:last-child {
			flex-grow: 1;
			padding: {
				left: 1.5625em;
				right: 1.5625em; }

			background-color: rgba($blue, 0.2); }

		@include media(575px) {
			flex-basis: 100%;
			&:first-child,
			&:last-child {
				padding: 2em 1em; } } }

	&__label {
		display: block;
		font-weight: 300;
		font-size: 0.875em;

		&_transp {
			opacity: 0.5; } }

	&__term {
		display: block;
		color: #ffffff;
		font-size: 1.5em;
		font-weight: 600;
		line-height: 1.67;
		white-space: nowrap; }

	&__button {
		min-width: 6.25em;
		margin: {
			top: 1.33em; }
		padding: {
			left: 2.08em;
			right: 2.08em; }
		font-size: 0.75em; }

	&__text {
		display: block;
		margin: {
			bottom: 24px; }
		font-size: 1.25em;
		line-height: 1.875;
		border-bottom: 1px solid #c0d1da;
		white-space: nowrap;

		&:last-child {
			margin-bottom: 0; } } }
