.plan {
	@extend %default-block;
	background-color: #f6fafc;

	&__list {
		display: flex;
		margin-top: (70em / 18);

		@include media(767px) {
			flex-wrap: wrap;
			margin-top: 40px; } }

	&__item {
		flex: 1 1 30%;
		margin-right: 30px;

		&:nth-child(3n + 3) {
			margin-right: 0; }

		@include media(1199px) {
			margin-right: 10px; }

		@include media(767px) {
			flex-basis: 100%;
			margin-right: 0;
			margin-bottom: 20px; } } }
