.gallery {
	padding: {
		top: (116em / 18);
		bottom: (150em / 18); }
	font-size: 18px;

	@media screen and (min-width: 768px) and (max-width: 1600px) {
		font-size: 16px; }

	@media screen and (min-width: 1200px) and (max-width: 1729px) {
		padding-left: 138px; }

	@include media(767px) {
		padding: {
			top: 40px;
			bottom: 40px; } }

	&__title {
		color: $blue; }


	&__container {
		max-width: 1406px;
		margin: auto;
		padding: {
			top: (60em / 18); }

		.swiper-slide {
			height: auto; }

		@media screen and (min-width: 1200px) and (max-width: 1729px) {
			margin-left: 0;
			padding-right: 30px; }

		@include media(1199px) {
			padding: {
				top: 20px;
				left: 30px;
				right: 30px; } }

		@include media(575px) {
			padding: {
				left: 15px;
				right: 15px; } } }

	&__category {
		display: flex;
		flex-wrap: wrap;
		height: 100%;
		background-color: #fff; }

	&__item {
		flex-basis: 25%;
		max-width: 25%;
		padding: 1px;
		box-sizing: border-box;
		line-height: 0;

		img {
			max-width: 100%;
			transition: transform 2s; }

		a {
			display: inline-block;
			line-height: 0;
			overflow: hidden;

			&:hover img {
				transition: transform 8s;
				transform: scale(1.2); } }

		@include media(1199px) {
			flex-basis: 33.3333%;
			max-width: 33.3333%; }

		@include media(767px) {
			flex-basis: 50%;
			max-width: 50%; } }

	&__controls {
		text-align: right;
		@include media(767px) {
			margin-top: 1em; } }

	&__button {
		margin-right: (78em / 18);
		padding: 0;

		font-size: (20em / 18);
		font-weight: 300;
		color: inherit;
		border: none;
		background: none;
		appearance: none;

		cursor: pointer;

		&:last-child {
			margin-right: 0; }

		&_active {
			font-weight: 600; }

		&:focus {
			outline: none; }

		@include media(767px) {
			margin-right: (30em / 18); } } }
