.business-offer {
	@extend %default-block;

	&_coffee-subs {
		padding-top: (80em / 18);

		@include media(767px) {
			padding-top: 40px; } }

	&__title {
		text-align: center; }

	&__list {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		margin-top: (70em / 18);

		@include media(767px) {
			margin-top: 40px; } }

	&__card {
		flex-basis: calc(50% - 15px);
		@extend .offer-tile;

		@include media(767px) {
			flex-basis: 100%;
			margin-bottom: 20px; } }

	&__wrapper {
		position: relative;
		z-index: 1;
		display: flex;
		flex-direction: column;
		justify-content: flex-end;
		align-items: flex-start;

		min-height: (735em / 18);
		padding: (50em / 18);

		box-sizing: border-box;

		@include media(1199px) {
			min-height: (600em / 18);
			padding: (30em / 18); }

		@include media(575px) {
			min-height: (420em / 18);
			padding: 15px; } }

	&__text {
		margin-bottom: (40em / 22);
		font-size: (22em / 18);
		line-height: (33 / 22); } }
