.referral {
	font-size: 16px;

	&__box {
		display: flex;
		align-items: center;
		padding: 2.75em 3.5em;

		background-color: #ffffff;
		font-size: 20px;
		font-weight: 300;

		span {
			white-space: nowrap; }

		@include media(1919px) {
			padding: 2.8vw 3.65vw; }

		@include media(1419px) {
			flex-wrap: wrap; } }

	&__link {
		flex: 1 1 auto;
		display: flex;
		max-width: 100%;
		margin-left: 2.5em;

		@include media(1419px) {
			flex-basis: 100%;
			margin-left: 0;
			margin-top: 1em; } }

	&__subtitle {
		margin: {
			top: 35px;
			bottom: 10px; }
		color: $blue;
		font-family: "Mont";
		font-size: 1.875em;
		font-weight: 800;
		line-height: 2; }

	&__desc {
		color: #0e3d5e;
		font-size: 18px;
		font-weight: 300; }

	&__table {
		margin: {
			top: 40px; } } }

.ref-link {
	display: flex;
	align-items: center;
	width: 100%;
	padding: {
		left: 25px; }

	border-radius: 35px;
	background-color: rgba($blue, 0.1);
	box-sizing: border-box;

	@include media(575px) {
		padding-left: 10px; }

	&__icon {
		width: 25px;
		height: 25px;

		@include media(575px) {
			display: none; } }

	&__link {
		flex: 1 1 auto;
		padding: {
			left: 1.75em;
			right: 2.75em; }

		font-size: 0.8em;
		white-space: nowrap;
		color: $blue;
		box-sizing: border-box;
		overflow: hidden;
		text-overflow: ellipsis;
		border: none;
		background: none;
		appearance: none;

		&:focus {
			outline: none; }

		&::selection {
			background-color: $blue;
			color: #fff; }

		@include media(575px) {
			padding: {
				left: 10px;
				right: 10px; } } }

	&__button {
		min-width: 8.5em;
		margin-right: 0;
		margin-left: auto;

		@include media(575px) {
			min-width: 7em; } } }
