.payment {
	display: flex;
	justify-content: space-between;


	color: $darkblue;

	@include media(991px) {
		flex-wrap: wrap; }

	&__box {
		padding: {
			bottom: 39px; }

		background-color: #ffffff;
		box-sizing: border-box;

		&_order {
			flex-basis: calc(66.666% - 15px);
			padding: {
				left: 20px; }

			@include media(991px) {
				flex-basis: 100%; }

			@include media(767px) {
				padding: {
					left: 0; } } }

		&_pay {
			flex-basis: calc(33.33% - 15px);

			@include media(991px) {
				flex-basis: 100%; } }

		@include media(991px) {
			margin: {
				bottom: 40px; } }

		@include media(575px) {
			margin: {
				bottom: 20px; } } }

	&__row {
		display: flex;
		justify-content: space-between;
		align-items: center;

		&_head {
			padding: {
				top: 15px;
				bottom: 15px; }
			border-bottom: 1px solid #f0f3f4; }

		&_order {
			padding: {
				left: 28px;
				right: 48px; }

			@include media(767px) {
				padding: {
					left: 20px;
					right: 20px; } }

			@include media(575px) {
				padding: {
					left: 15px;
					right: 15px; } } }

		&_pay {
			padding: {
				left: 10px;
				right: 10px; }

			@include media(991px) {
				padding: {
					left: 28px;
					right: 40px; } }

			@include media(767px) {
				padding: {
					left: 20px;
					right: 20px; } }

			@include media(575px) {
				padding: {
					left: 15px;
					right: 15px; } } }

		@include media(575px) {
			flex-wrap: wrap; } }

	&__title {
		font-size: 25px;
		font-weight: 600;

		@include media(575px) {
			font-size: 20px; } }

	&__edit-cart {
		position: relative;

		padding: 0;
		border: none;
		background: none;
		appearance: none;

		color: $blue;
		font-family: "Poppins";
		font-size: 1.125em;
		font-weight: 300;
		line-height: 1.57;

		&::after {
			content: '';
			position: absolute;
			bottom: 0.1em;
			left: 0;

			width: 100%;
			height: 1px;
			background-color: $blue; }

		&:focus {
			outline: none; }

		@include media(575px) {
			font-size: 1em; } }

	&__product {

		padding: {
			left: 28px;
			right: 50px; }

		border-bottom: 1px solid #f0f3f4;

		@include media(767px) {
			padding: {
				left: 20px;
				right: 20px; } }

		@include media(575px) {
			padding: {
				left: 15px;
				right: 15px; } } }


	&__col {
		padding: {
			top: 30px; }

		&_more {
			line-height: 1.5;
			p {
				margin-bottom: 5px; } }

		@include media(575px) {
			flex-basis: 100%; } }

	&__address {
		margin: {
			bottom: 7px; }

		line-height: 1.3;
		font-size: 1.125em;
		strong {
			display: block; } }

	&__sn {
		font-size: 1.375em;
		line-height: 1.5; }

	&__total {
		display: flex;
		flex-direction: column;
		align-items: flex-end;
		padding: {
			top: 30px;
			bottom: 60px;
			right: 50px; }

		@include media(767px) {
			padding: {
				left: 20px;
				right: 20px; } }

		@include media(575px) {
			padding: {
				left: 15px;
				right: 15px;
				bottom: 30px; } } }


	&__line {
		display: flex;
		justify-content: space-between;
		align-items: center;
		min-width: 230px;
		margin-bottom: 15px;

		h4 {
			font-size: 0.875em; }

		@include media(575px) {
			flex-basis: 100%;
			min-width: 100%; } }

	&__final-price {
		font-size: 1.25em;
		font-weight: 700; }

	&__card-details {
		padding: {
			left: 10px;
			right: 10px; }

		@include media(991px) {
			padding: {
				left: 28px;
				right: 40px; } }

		@include media(767px) {
			padding: {
				left: 20px;
				right: 20px; } }

		@include media(575px) {
			padding: {
				left: 15px;
				right: 15px; } } }

	&__form {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		padding: {
			top: 20px;
			right: 20px; }

		@include media(767px) {
			padding: {
				top: 15px;
				right: 0; } } }

	&__field {
		display: flex;
		flex-direction: column;
		justify-content: flex-end;
		flex-basis: 100%;
		margin-bottom: 20px;

		&_third {
			flex-basis: calc(33.3333% - 16px); }

		.input-text__input {
			border-bottom-color: #f0f3f4;

			&:focus {
				border-bottom-color: darken(#f0f3f4, 10%); } } }

	&__submit {
		flex-basis: 100%;
		margin-top: 30px; } }
