.blue-box {
	padding-top: 50px;
	padding-bottom: 60px;

	@include media(991px) {
		position: absolute;
		left: 15px;
		right: 15px;
		top: 15px;
		z-index: 10;

		transform: scale(0.95);
		opacity: 0;
		pointer-events: none;
		transition: transform 0.3s, opacity 0.3s;
		box-shadow: 0 2px 23px 3px rgba(49, 90, 112, 0.06);
		background-color: #fff;

		&.visible {
			transform: scale(1);
			opacity: 1;
			pointer-events: auto; } }

	&__close {
		position: absolute;
		top: 10px;
		right: 10px;

		display: none;
		width: 16px;
		height: 16px;
		padding: 0;
		border: none;

		background: none;
		appearance: none;

		transition: box-shadow 0.2s;

		&:focus {
			outline: none;
			box-shadow: 0 0 0 1px #fff, 0 0 4px 2px $blue; }

		&::before {
			@extend %cls;
			transform: translate(-50%, -50%) rotate(-45deg); }

		&::after {
			@extend %cls;
			transform: translate(-50%, -50%) rotate(45deg); }

		@include media(991px) {
			display: inline-block; } }

	&__container {
		border-top: 1px solid #e0e8eb;

		@include media(991px) {
			border-top: none;
			padding: 10px; } }

	&__filters {
		display: flex;
		justify-content: space-between;
		align-items: center;
		padding: {
			top: 30px;
			bottom: 30px; }

		@include media(991px) {
			overflow-x: auto; } }

	&__option {
		flex-shrink: 0;
		padding-right: 12px;

		&:last-child {
			padding-right: 5px; }

		&_category {
			position: relative; } }

	&__select {

		select:not([data-ss-uuid]) {
			padding: 10px 35px 10px 15px;
			border-radius: 25px;
			border: 2px solid #64b2db;

			+ .select__arrow {
				right: 15px;

				color: $blue;

				@include media(767px) {
					right: 15px;

					width: auto;
					height: auto;

					background: none; } } }


		select,
		.style-select .ss-selected-option {
			color: $blue; } }

	&__button {
		padding: 14px 22px 13px;
		border: none;

		font-size: 16px;
		white-space: nowrap;

		border-radius: 25px;
		background: rgba($blue, 0);
		color: #64b2db;
		appearance: none;

		transition: box-shadow 0.2s;

		&_active {
			background: rgba($blue, 0.2); }

		&:focus,
		&:hover {
			outline: none;
			box-shadow: 0 0 0 2px #fff, 0 0 4px 4px $blue; } }

	&__goods {
		display: flex;
		flex-wrap: wrap; }

	&__card {
		flex-basis: calc(20% - 10px);
		max-width: calc(20% - 10px);
		margin-bottom: 20px;
		margin-right: 12.5px;

		@media screen and (min-width: 1200px) {
			&:nth-child(5n + 5) {
				margin-right: 0; } }

		@include media(1199px) {
			flex-basis: calc(25% - 10px);
			max-width: calc(25% - 10px);
			margin-right: 7.5px;

			@media screen and (min-width: 640px) {
				&:nth-child(4n + 4) {
					margin-right: 0; } } }

		@include media(991px) {
			margin-bottom: 10px; }

		@include media(639px) {
			flex-basis: calc(33.3333% - 10px);
			max-width: calc(33.3333% - 10px);
			margin-right: 6.66px;

			@media screen and (min-width: 640px) {
				&:nth-child(3n + 3) {
					margin-right: 0; } } }

		@include media(575px) {
			flex-basis: calc(50% - 10px);
			max-width: calc(50% - 10px);
			margin-right: 10px;

			&:nth-child(even) {
				margin-right: 0; } } }

	&__choose {
		display: none;
		margin-top: 75px;

		text-align: center;

		@include media(991px) {
			display: block; } } }

%cls {
	content: '';
	position: absolute;
	left: 50%;
	top: 50%;

	width: 21px;
	height: 2px;

	background-color: $blue; }

.bb-card {
	position: relative;

	padding-top: 10px;

	text-align: center;
	cursor: grab;
	box-sizing: border-box;

	transition-property: box-shadow, background-color;
	transition-duration: 0.3s;

	@include media(991px) {
		padding: 10px 5px;
		border: 2px solid transparent;

		box-shadow: 0 0 10px #fff;

		&_checked {
			z-index: 20;

			border-color: $blue; } }

	&:hover {
		@media screen and (min-width: 992px) {
			box-shadow: 0 2px 23px 3px rgba(49, 90, 112, 0.1); } }

	&:hover & {

		&__hidden {

			@media screen and (min-width: 992px) {
				opacity: 1;
				pointer-events: auto; } } }

	&:active {
		cursor: grabbing; }

	&__img {
		position: relative;
		z-index: 2;

		display: flex;
		justify-content: center;
		align-items: center;
		height: 220px;

		>div, >picture {
			display: flex;
			justify-content: center;
			align-items: center;
			height: 100%;
			width: 100%; }

		img {
			max-width: 100%;
			min-height: 1px;
			max-height: 100%; } }

	&__title {
		padding: 10px 0;

		font-size: 14px;
		font-weight: 300;
		letter-spacing: -0.17px;
		line-height: (22 / 14);

		@include media(575px) {
			font-size: 12px; } }

	&__price {
		font-size: 18px;
		font-weight: 600;
		letter-spacing: 2.84px;
		line-height: 1;

		text-transform: uppercase;
		white-space: nowrap;
		color: $blue; }

	&__hidden {
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		z-index: 1;

		display: flex;
		align-items: flex-end;
		justify-content: center;
		padding: {
			bottom: 30px;
			left: 9%;
			right: 9%; }

		background-color: #ffffff;
		opacity: 0;
		pointer-events: none;

		transition: opacity 0.3s; }

	&__icon {
		position: absolute;
		right: 10px;
		top: 10px;

		width: 16px;
		height: 16px; }

	&__add-to-bb {
		flex: 1 1 auto;
		padding-left: 10px;
		padding-right: 10px;

		font-size: 12px;

		@include media(1199px) {
			font-size: 10px; } } }
