.faq {
	width: 100%;
	min-height: 100vh;
	padding: {
		top: 95px;
		left: 117px;
		bottom: 95px;
		right: 80px; }

	box-sizing: border-box;

	font-size: 20px;

	@include media(1399px) {
		padding: {
			top: 3em;
			left: 3.1em;
			right: 3em;
			bottom: 3em; }

		font-size: 16px; }

	@include media(1199px) {
		padding: 40px 48px; }

	@include media(767px) {
		padding: {
			top: 20px;
			bottom: 50px;
			left: 16px;
			right: 16px; }

		text-align: center; }

	&__list {
		position: relative;

		height: 17.25em;
		margin-top: 4.2em;
		padding-right: 2.75em;

		box-sizing: border-box;

		li {
			margin-bottom: 1.65em;

			&.active {
				margin-bottom: 0.85em; }

			&:last-child {
				margin-bottom: 0; } }

		@include media(1199px) {
			height: auto;
			margin-top: 20px;
			padding-right: 0;

			overflow: visible;

			li {
				margin-bottom: 10px;
				padding: 20px 10px;

				background-color: #fff;
				box-shadow: 0 2px 23px 3px rgba(49, 90, 112, 0.06);

				&.active {
					margin-bottom: 10px;
					padding: {
						top: 15px;
						bottom: 15px; } } } }

		@include media(767px) {
			text-align: left; } }

	&__question {
		display: flex;
		justify-content: space-between;
		align-items: center;
		width: 100%;

		color: #2a2727;
		font-size: 1em;
		cursor: pointer;
		user-select: none;

		&:focus {
			outline: none; }

		&.active {
			color: $blue;
			font-weight: bold;

			+ .faq__answer {
				display: block; } }

		&.active & {

			&-icon {
				background-color: rgba(#64b2db, 0.1);

				span {
					&:nth-child(1) {
						opacity: 0;
						transform: translate(-50%, -50%) rotate(-90deg); }
					&:nth-child(2) {
						opacity: 1;
						transform: translate(-50%, -50%); } } } }

		&-icon {
			position: relative;
			display: inline-block;

			width: 1.85em;
			height: 1.85em;

			border-radius: 50%;
			background-color: transparent;

			pointer-events: none;

			span {
				position: absolute;
				left: 50%;
				top: 50%;
				transform: translate(-50%, -50%);
				transition: transform 0.3s, opacity 0.3s;

				&:nth-child(2) {
					opacity: 0;
					transform: translate(-50%, -50%) rotate(90deg); } }

			@include media(1199px) {
				background-color: rgba(#64b2db, 0.1); }

			@include media(767px) {
				font-weight: bold;
				color: $blue; } } }

	&__answer {
		display: none;
		max-width: 29.5em;

		padding-top: 1.2em;

		color: $blue;
		font-size: 1em;
		font-weight: 300;

		line-height: 1.5;
		color: $blue; }

	&__load-more {
		display: none;

		@include media(1199px) {
			display: inline-block;
			margin-top: 39px;
			padding: 0;
			border: none;

			background: none;
			color: #64b2db;
			font-family: Poppins;
			font-size: 18px;
			font-weight: bold;
			text-decoration: underline;

			apearance: none; } }


	&__subt {
		margin-top: 4.95em;
		color: #2a2727;
		font-family: Poppins;
		font-size: 1.3em;
		font-weight: 800;

		@include media(1199px) {
			margin-top: 2.6em;

			font-size: 1.25em;
			line-height: 1.2; } }

	&__write {
		min-width: 8.55em;
		margin-top: 1.8em;

		@include media(1199px) {
			min-width: 10.68em; } } }
