.sidebar {
	position: absolute;
	left: 0;
	top: 0;
	z-index: 5;

	min-width: (310em / 18);
	max-width: (343em / 18);
	height: 100%;

	font-size: 18px;

	box-sizing: border-box;
	background-color: #ffffff;

	@include media(991px) {
		transition: transform 0.3s;
		transform: translateX(-100%);

		&.visible {
			transform: translateX(0); } }

	&_bluebox {
		@include media(991px) {
			position: static;

			max-width: 100%;
			padding-bottom: 50px;

			transform: none; } }

	@media screen and (min-width: 1200px) and (max-width: 1600px) {
		font-size: 16px; } }
