.select {
	position: relative;
	select {
		&:not([data-ss-uuid]) {
			width: 100%;
			padding-right: 20px;
			border: none;

			font-family: inherit;
			font-size: inherit;
			appearance: none;
			box-sizing: border-box;

			@include media(767px) {
				width: 100%;
				margin-left: 0;
				padding: {
					left: 14px;
					top: 17px;
					bottom: 12px;
					right: 45px; }

				box-shadow: 0 2px 23px 3px rgba(49, 90, 112, 0.06);
				background-color: #ffffff; }

			@include media(349px) {
				font-size: 16px; } } }

	select,
	.style-select .ss-selected-option {
		font-weight: 700;
		text-transform: uppercase;

		box-shadow: none;
		color: $darkblue; }

	&__arrow {
		position: absolute;
		right: 0;
		top: 47%;

		pointer-events: none;

		transform: translateY(-50%);

		svg {
			width: 12px;
			height: 7px; }

		@include media(767px) {
			right: 15px;
			width: 29px;
			height: 29px;

			display: flex;
			justify-content: center;
			align-items: center;
			border-radius: 50%;

			background-color: rgba($blue, 0.1); } }

	.style-select.open .ss-selected-option {
		box-shadow: 0 1px 4px rgba(9, 48, 76, 0.09); }

	.style-select + .select__arrow {
		display: none; } }
