.events {
	padding: {
		top: 50px;
		bottom: 120px; }

	font-size: 18px;

	@media screen and (min-width: 768px) and (max-width: 1600px) {
		font-size: 16px; }

	@media screen and (min-width: 1200px) and (max-width: 1459px) {
		padding-left: 8vw; }

	@include media(767px) {
		padding: {
			top: 40px;
			bottom: 40px; } }

	&__header {
		display: flex;
		justify-content: space-between;

		@include media(767px) {
			display: block; } }

	&__category {
		display: flex;
		align-items: center;

		@include media(767px) {
			display: block;
			span {
				display: block;
				margin-bottom: 6px; } } }

	&__select {
		position: relative;
		margin-left: 17px;

		@include media(767px) {
			margin-left: 0; } }

	&__month {
		display: flex;
		align-items: center;
		padding: {
			right: 20px; }

		@include media(767px) {
			justify-content: center;
			margin-top: 20px;
			padding-right: 0; } }

	&__control {
		display: inline-flex;
		justify-content: center;
		align-items: center;
		width: 30px;
		height: 30px;
		padding: 0;
		border: 1px solid #dadfea;
		box-sizing: border-box;

		border-radius: 50%;
		background-color: #ffffff;
		color: $blue;
		appearance: none;

		svg {
			width: 12px;
			height: 7px; }

		&:hover {
			background-color: $blue;
			color: #fff; }

		&_prev {
			transform: rotate(90deg); }

		&_next {
			transform: rotate(-90deg); } }

	&__selected-month {
		margin: {
			left: 30px;
			right: 30px; }

		font-size: (20em / 18);
		font-weight: 600;

		color: $blue;
		user-select: none; }

	&__calendar {
		margin-top: 30px; }

	&__mobile-event {
		display: none;
		margin-top: 30px;
		padding: 20px;
		background-color: #f6fafc;

		@include media(767px) {
			display: block; } } }
