.subscription-form {
	text-align: center;
	text-transform: uppercase;

	transition: box-shadow 0.3s;

	box-shadow: 0 2px 11px 8px rgba(79, 156, 194, 0.05);

	&:hover {
		box-shadow: 0 2px 11px 8px rgba(79, 156, 194, 0.26); }

	&__header {
		padding: {
			top:  (30em / 18);
			bottom: (50em / 18);
			left: 15px;
			right: 15px; }
		background-color: #64b2db;
		color: #fff;

		@include media(991px) {
			font-size: 14px; } }

	&__plan {
		font-size: (30em / 18);
		line-height: 1.5; }

	&__plan-desc {
		display: flex;
		justify-content: center;

		margin-top: 10px;

		svg {
			width: 13px;
			height: 15px;

			margin-right: (20em / 18); } }

	&__plan-pricing {
		margin: {
			top: 1em; }

		font-size: (46em / 18);
		font-weight: 600;
		text-transform: uppercase;

		color: #ffffff; }

	&__options {
		padding: {
			top: 0.5em;
			left: 20px;
			right: 20px;
			bottom: (90em / 16); }

		font-size: 0.89em;

		background-color: #fff; }

	&__subt {
		margin: 2em 0;

		font-size: (13em / 16);
		font-weight: 300;
		letter-spacing: (2em / 16); }

	&__terms {
		display: flex;
		justify-content: space-between;

		li {
			position: relative;
			flex: 1 1 30%;
			margin-right: 10px;

			&:nth-child(3) {
				margin-right: 0; } } }

	&__realinput {
		position: absolute;
		top: 100%;
		left: 0;

		width: 1px;
		height: 1px;
		padding: 0;
		border: none;

		background: none;
		overflow: hidden;
		appearance: none;

		&:focus {
			outline: none;

			+ .subscription-form__label {
				border-color: $blue; } }

		&:checked {

			+ .subscription-form__label {
				background-color: $blue;
				color: #fff; } } }

	&__label {
		display: block;
		padding: {
			top: 12px;
			bottom: 10px; }

		border: 1px solid rgba($blue, 0.2);
		background-color: rgba($blue, 0.2);
		color: $blue;

		font-weight: 300;
		line-height: 18px;
		font-size: (30em / 16);

		span {
			display: block;
			margin-top: 0.5em;
			font-size: 0.5em; } }

	&__select {

		border-bottom: 1px solid rgba($blue, 0.1);

		.style-select {
			width: 100%;
			font-weight: 300;

			.ss-selected-option {
				font-weight: inherit; } } }

	&__submit {
		margin-top: (65em / 16); } }

