.tutorials {
	display: flex;
	flex-wrap: wrap;
	padding-top: 2.5em;
	padding-bottom: 130px;

	font-size: 18px;

	@media screen and (min-width: 1200px) and (max-width: 1600px) {
		font-size: 16px; }

	@include media(767px) {
		padding-bottom: 70px; }

	@include media(575px) {
		padding-bottom: 40px; }

	&__item {
		flex-basis: calc(50% - 15px);
		margin-right: 30px;
		margin-bottom: 20px;

		@include media(767px) {
			flex-basis: calc(50% - 10px);
			margin-right: 20px; }

		@include media(575px) {
			flex-basis: 100%;
			margin-right: 0; }

		&:nth-child(even) {
			margin-right: 0; } } }

.tutorial-tile {
	height: 100%;
	@extend .offer-tile;

	&__wrapper {
		@extend .offer-tile__wrapper;
		min-height: 40.5em;
		padding: 50px 40px;

		@include media(1199px) {
			min-height: 33em; }

		@include media(767px) {
			min-height: 28em;
			padding: 30px 20px;
			font-size: 16px; } }

	&_mini &__wrapper {
		min-height: 26.27em;
		padding: {
			left: 30px;
			right: 30px; }

		@include media(767px) {
			padding: {
				left: 15px;
				right: 15px; } } }

	&__title {
		font-size: 1.66em;
		font-weight: 500;
		line-height: 1.1;
		text-transform: uppercase; }

	&__desc {
		max-width: 15em;
		min-height: 2.5em;
		margin-top: 0.5em;

		font-size: 1.22em;
		font-weight: 300;
		line-height: 1.5; }

	&__button {
		min-width: 12.19em;
		margin-top: 1.875em; } }
