.what-do-we-offer {
	padding: {
		top: (114em / 18);
		bottom: (160em / 18); }

	font-size: 18px;

	background-color: #f6fafc;
	text-align: center;

	&_pt-80 {
		padding-top: (80em / 16); }

	@media screen and (min-width: 768px) and (max-width: 1600px) {
		font-size: 16px; }

	@media screen and (min-width: 1200px) and (max-width: 1459px) {
		padding-left: 8vw; }

	@include media(767px) {
		padding: {
			top: 40px;
			bottom: 40px; } }

	&__container {
		position: relative; }

	&__offers {
		padding: {
			top: (70em / 18);
			bottom: (30em / 18); }

		@include media(767px) {
			padding: {
				top: 40px; } } }

	&__full-list {
		display: flex;
		justify-content: space-between;
		flex-wrap: wrap;
		padding: {
			top: (100em / 18); }

		border-top: 1px solid rgba($darkblue, 0.3);

		@include media(767px) {
			padding: {
				top: 40px; } } } }
