.scroll-panel {
	position: fixed;
	top: 0;
	bottom: 0;
	left: 0;
	z-index: 15;

	display: flex;
	flex-direction: column;
	justify-content: space-between;
	align-items: center;
	max-width: 8vw;
	width: 138px;
	padding: {
		top: 30px;
		bottom: 30px; }
	border-right: 1px solid $blue;

	box-sizing: border-box;

	&_abs {
		position: absolute; }

	&_footer {
		.slide-btn {
			width: auto; }

		.slide-btn__up {
			position: static;
			transform: none; } }

	@include media(1199px) {
		display: none; }

	&__indicator {
		display: inline-flex;
		flex-direction: column;
		justify-content: center;
		align-items: center; }

	&__num {
		writing-mode: vertical-rl;
		transform: scale(-1);
		color: $blue; }

	&__track {
		position: relative;

		width: 1px;
		height: 28.4vh;
		margin-top: calc(3vh + 10px);
		background: $blue;
		opacity: 0.5; }

	&__thumb {
		position: absolute;
		top: 0;
		left: -1px;

		width: 3px;
		height: 50px;

		background-color: $blue;

		transition: transform 0.3s linear; }


	&__logo {
		display: inline-block;
		width: 100%;

		color: $blue;

		transition: color 0.3s;

		svg {
			width: 100%;
			height: 90px; }

		@include media(1600px) {
			svg {
				height: 60px; } }



		&:hover {
			color: lighten($blue, 10%); } }


	&__slide-to {
		width: 27px;
		padding: 0;

		border: none;

		color: $blue;
		font-family: "Poppins";
		font-size: 18px;
		font-weight: 300;

		appearance: none;
		background: none;
		cursor: pointer; } }

.slide-btn {

	&__text {
		writing-mode: vertical-rl;
		transition: transform 0.2s;
		transform: scale(-1); }

	&__down {
		display: inline-block;

		svg {
			width: 12px;
			height: 6px; }

		transition: transform 0.3s;
		animation: verticalBounce 10s ease infinite; }

	&__up {
		position: absolute;
		bottom: 30px;
		left: 50%;

		display: flex;
		justify-content: center;
		align-items: center;
		width: 45px;
		height: 45px;

		transition-property: transform, background-color, color;
		transition-duration: 0.3s;
		transform-origin: 50% 50%;
		transform: translateX(-50%) scale(0) rotate(90deg);
		border-radius: 50%;
		box-sizing: border-box;

		background-color: $blue;
		color: #fff;

		svg {
			width: 14px;
			height: 21px; } }

	&:hover & {

		&__text {
			transform: scale(-1) translate3d(0, 10%, 0); }

		&__down {
			animation-play-state: paused; }

		&__up {
			background-color: #fff;
			color: $blue; } }

	&:focus {
		outline: none; }

	&_to-top & {
		&__text,
		&__down {
			transform: scale(0);
			animation: none;
			pointer-events: none; }
		&__up {
			transform: translateX(-50%) scale(1) rotate(0deg); } }

	&_to-top:hover & {
		&__text {
			transform: scale(0) translateY(-100%); } } }

@keyframes verticalBounce {
	0% {
		transform: translate3d(0, 0, 0); }
	2% {
		transform: translate3d(0, 5px, 0); }
	4% {
		transform: translate3d(0, 0, 0); }
	6% {
		transform: translate3d(0, 5px, 0); }
	8%, 100% {
		transform: translate3d(0, 0, 0); } }

