.turkich-products {
	@extend %default-block;

	background-color: #f6fafc;

	&__list {
		display: flex;
		margin-top: (80em / 18);

		@include media(767px) {
			flex-wrap: wrap; }

		@include media(575px) {
			margin-top: 40px; } } }


.alt-product-tile {
	flex: 1 1 auto;
	display: flex;
	min-width: calc(50% - 35px);
	margin-right: 50px;
	padding: {
		top: (50em / 18);
		bottom: (50em / 18); }

	background: linear-gradient(to left, white 85%, transparent 85%);

	@include media(767px) {
		flex-basis: 100%;
		margin-bottom: 20px;
		margin-right: 0; }

	@include media(575px) {
		flex-wrap: wrap;
		justify-content: center;
		padding: {
			top: 20px;
			bottom: 20px; }

		background: linear-gradient(to top, white 75%, transparent 75%); }

	&:nth-child(even) {
		margin-right: 0; }

	&__image {
		flex: 1 1 auto;
		display: flex;
		align-items: center;
		padding-right: 1em;

		box-sizing: border-box;

		img {
			max-width: 100%;
			max-height: 12.8em; }

		@include media(767px) {
			flex-basis: 60%; }

		@include media(575px) {
			flex-basis: 100%;
			justify-content: center; } }

	&__text {
		flex: 1 1 auto;

		@include media(767px) {
			flex-basis: 40%; }

		@include media(575px) {
			text-align: center; } }

	&__title {
		font-size: 1.39em;
		font-weight: 700;
		line-height: 1.68; }

	&__info {
		display: block;
		font-size: 1.11em;
		font-weight: 300;
		line-height: 1.5; }

	&__price {
		display: block;
		margin-top: 0.71em;

		font-size: 1.55em;
		font-weight: 600;

		text-transform: uppercase;
		color: $blue; }

	&__button {
		min-width: 7.5em;
		margin-top: 50px;

		@include media(575px) {
			min-width: 215px; } } }
