.product-tile {
	position: relative;

	height: 100%;

	text-align: center;

	background-color: #ffffff;
	box-sizing: border-box;

	// transition: box-shadow 0.3s

	&_has-shadow {
		box-shadow: 0 2px 11px 8px rgba(79, 156, 194, 0.05); }

	&_full-img & {

		&__image {

			img {
				@supports (object-fit: cover) {
					width: 100%;
					height: 100%;
					object-fit: cover; } } } }

	&__content {
		position: relative;
		z-index: 2;
		padding: {
			bottom: (60em / 18); }

		box-sizing: border-box; }

	&__image {
		position: relative;

		width: 100%;
		padding-bottom: 90%;

		overflow: hidden;

		a, div, picture {
			position: absolute;
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
			width: 100%;
			height: 100%; }

		img {
			max-width: 100%;
			max-height: 100%;
			min-height: 1px; } }

	&__name {
		padding: 1em;
		color: $darkblue;
		font-weight: 300;
		line-height: 22px;
		overflow: hidden;

		a {
			text-decoration: none;
			color: inherit; }

		@include media(1299px) {
			letter-spacing: -0.2px; } }

	&__price {
		position: absolute;
		bottom: 30px;
		left: 50%;

		font-size: (19em / 18);
		font-weight: 600;
		letter-spacing: 3px;
		text-transform: uppercase;
		white-space: nowrap;

		transform: translateX(-50%);

		color: $blue; }

	&__hidden {
		position: absolute;
		top: 0;
		left: 0;
		z-index: 0;

		width: 100%;
		height: calc(100% + 7.85em);

		font-size: 14px;

		background-color: #fff;
		box-shadow: 0 12px 26px 7px rgba(71, 121, 147, 0.1);
		opacity: 0;
		pointer-events: none;

		transition: opacity 0.3s;

		@include media(1359px) {
			font-size: 12px; }

		@include media(575px) {
			font-size: 8px; }

		@include media(469px) {
			height: calc(100% + 10.5em); } }

	&__buttons {
		position: absolute;
		bottom: 0;
		left: 0;

		display: flex;
		align-items: center;
		justify-content: center;
		width: 100%;
		padding: {
			bottom: (50em / 14);
			left: 10px;
			right: 10px; }

		box-sizing: border-box;

		button {
			flex: 1 0 auto;
			min-width: 1px;

			margin-right: 10px;
			padding: {
				left: 0.8em;
				right: 0.8em; }

			font-size: inherit;

			&:last-child {
				margin-right: 0; }

			@include media(575px) {
				margin-right: 5px; } }

		@include media(575px) {
			padding: {
				bottom: 15px; } }

		@include media(469px) {
			flex-wrap: wrap;

			button {
				flex-basis: 100%;
				margin-bottom: 5px;
				margin-right: 0;
				&:last-child {
					margin-bottom: 0; } } } }

	&:hover {
		z-index: 50;
		box-shadow: none; }

	&:hover & {

		&__hidden {
			opacity: 1;
			pointer-events: auto; } } }
