.radio-switch {
	position: relative;

	&__input {
		position: absolute;
		left: 12px;
		bottom: 0;

		width: 1px;
		height: 1px;
		margin: 0;
		padding: 0;

		overflow: hidden;
		appearance: none;

		&:focus {
			outline: none; }

		&:checked {

			& + .radio-switch__track {
				background-color: $blue;

				.radio-switch__thumb {
					transform: translate3d(100%, 0, 0); } } } }

	&__track {
		display: inline-block;
		width: 54px;
		height: 28px;
		padding: 1px;

		border-radius: 14px;
		background-color: #dfe7eb;
		box-sizing: border-box;
		line-height: 0;

		cursor: pointer;

		transition: background-color 0.3s; }

	&__thumb {
		width: 26px;
		height: 26px;

		border-radius: 50%;
		background-color: #ffffff;
		transition: transform 0.3s; } }
