.lets-work {
	@extend %default-block;
	background-color: #f6fafc;

	text-align: center;

	&__note {
		margin-top: 1.5em;
		font-size: (20em / 18); }

	&__form {
		max-width: (462em / 18);
		margin: (70em / 18) auto 0;
		padding: {
			top: (30em / 18);
			left: (50em / 18);
			right: (50em / 18);
			bottom: (60em / 18); }

		box-shadow: 0 0 16px 14px rgba(232, 237, 240, 0.18);
		background-color: #ffffff;
		box-sizing: border-box;

		@include media(767px) {
			padding: {
				top: 40px;
				left: 15px;
				right: 15px;
				bottom: 40px; }


			.field:first-child {
				margin-top: 0; } } } }
