.advantages {
	@extend .about-us;
	background-color: #f6fafc;

	&__list {
		text-align: center; }

	&__item {
		padding: {
			left: 10px;
			right: 10px; }

		font-size: (16em / 18);

		box-sizing: border-box;

		@include media(1199px) {
			max-width: (260em / 16); } }

	&__icon-wrapper {
		display: flex;
		justify-content: center;
		align-items: flex-end;
		height: 90px; }

	&__icon {
		color: $blue;

		&_coffeemaker {
			width: 79px;
			height: 91px; }
		&_coffee-cups {
			width: 84px;
			height: 91px; }
		&_coffee-grinder {
			width: 79px;
			height: 91px; }
		&_espresso-machine {
			width: 84px;
			height: 84px; } }

	&__title {
		margin: {
			top: (30em / 18); }
		font-size: (20em / 18);
		font-weight: 700;
		text-transform: uppercase; }

	&__text {
		margin: {
			top: (30em / 18); } } }
