.offers {
	margin: {
		bottom: (70em / 18);
		left: auto;
		right: auto; }

	overflow: visible;

	font-size: 18px;

	@include media(767px) {
		margin: {
			bottom: 40px; } }

	&__queue {
		display: inline-flex;
		@media screen and (min-width: 1200px) {
			flex-wrap: wrap; } }

	&__item {

		@media screen and (min-width: 1200px) {
			max-width: calc(33.3333% - 20px);
			flex-basis: calc(33.3333% - 20px);
			margin: {
				right: 30px;
				bottom: 30px; }

			&:nth-child(3n + 3) {
				margin-right: 0; } }

		@include media(1199px) {
			width: (255em / 18); } }

	.swiper-pagination {
		position: static;
		margin-top: (57em / 18);

		@include media(767px) {
			margin-top: 40px; }

		@media screen and (min-width: 1200px) {
			display: none; } } }
