.how-blue-bird-works {
	padding: {
		top: 130px;
		bottom: 105px; }
	background-color: #f6fafc;

	box-sizing: border-box;

	@include media(1699px) {
		padding: {
			left: calc(8vw + 30px);
			right: 30px;
			top: 102px;
			bottom: 61px; } }

	@include media(1199px) {
		padding: {
			left: 0;
			right: 0; } }

	@include media(767px) {
		padding: {
			top: 26px;
			bottom: 50px; } }

	&__container {
		max-width: 100%;
		width: 1415px;
		margin: auto;

		@include media(1699px) {
			margin-left: 0; } }

	&__title {
		padding-left: 10px;
		@include media(1199px) {
			padding-left: 30px;
			padding-right: 30px; } }


	&__steps {
		display: flex;
		justify-content: center;
		margin-top: 90px;
		padding: {
			top: 10px;
			bottom: 80px;
			left: 10px;
			right: 10px; }

		li {
			width: 255px;
			height: auto; }

		@include media(1699px) {
			margin: {
				top: 60px; } }

		@include media(1199px) {
			margin-top: 47px;
			padding: {
				left: 30px;
				right: 30px; } }

		@include media(767px) {
			flex-wrap: wrap;
			margin-top: 40px;
			padding-left: 15px;
			padding-bottom: 60px;
			padding-right: 15px; } }

	&__step {
		flex-basis: calc(25% - 22.5px);
		margin-right: 30px;

		&:last-child {
			margin-right: 0; }

		@include media(1199px) {
			flex-basis: calc(25% - 7.5px);
			margin-right: 10px;

			.step {
				padding: 20px;

				&__icon {
					width: 50px;
					height: 50px;
					margin-top: 90px; } } }

		@include media(767px) {
			flex-basis: calc(50% - 5px);
			margin-bottom: 10px;

			&:nth-child(even) {
				margin-right: 0; } }

		@include media(575px) {
			flex-basis: 100%;
			margin-right: 0; } } }

.steps-buy {
	display: flex;
	flex-direction: column;
	justify-content: flex-end;
	height: 100%;
	padding-bottom: 30px;
	padding-left: 23px;
	padding-right: 23px;

	box-shadow: 0 2px 11px 8px rgba(79, 156, 194, 0.05);
	background-color: #0e3d5e;
	box-sizing: border-box;

	transition: box-shadow 0.3s;

	&:hover {
		box-shadow: 0 2px 11px 8px rgba(79, 156, 194, 0.2); }

	&__text {
		font-family: "Mont";
		font-size: 18px;
		font-weight: 300;
		line-height: 26px;
		color: #fff;

		strong {
			color: $blue; } }


	&__button {
		width: 144px;
		margin-top: 76px; } }
