.title {
	font-family: "Mont";
	font-weight: 800;
	font-size: 50px;
	line-height: 62px;

	@include media(1600px) {
		font-size: 36px; }

	@include media(1199px) {
		font-size: 30px;
		line-height: 42px; }

	@include media(767px) {
		text-align: center; }

	&_center {
		text-align: center; } }

.alt-title {
	color: $blue;
	font-size: 30px;
	font-weight: 700;
	text-align: left;

	@include media(767px) {
		font-size: 24px; } }
