.breadcrumb {
	color: $darkblue;
	font-size: 1em;
	font-weight: 400;
	letter-spacing: 1px;
	line-height: 1.5;

	&__item {
		display: inline-flex;
		align-items: center;

		&::after {
			content: '';

			display: inline-block;
			width: 11px;
			height: 1.5em;
			margin: {
				left: 1.5em;
				right: 1.2em; }

			vertical-align: middle;

			background-image: url("data:image/svg+xml,%3Csvg width='11' height='8' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M7.407.12a.38.38 0 0 0-.554 0 .415.415 0 0 0 0 .568l2.81 2.907H.389A.393.393 0 0 0 0 3.997c0 .224.172.407.388.407h9.276L6.853 7.306a.422.422 0 0 0 0 .574.38.38 0 0 0 .554 0l3.477-3.596a.404.404 0 0 0 0-.568z' fill='%230e3d5e'/%3E%3C/svg%3E");
			background-position: center;
			background-repeat: no-repeat; }

		&:last-child::after {
			display: none; }


		a {
			color: inherit;
			text-decoration: none;

			&:hover {
				color: $blue; } } } }
